import React, { Component } from 'react'
import axiosConn from '../../../Global/axiosConn';
import { forwardRef } from 'react';
import { format } from "date-fns";
import MaterialTable from "material-table";
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import EditIcon from '@material-ui/icons/Edit';
import SaveIcon from '@material-ui/icons/Save';
import AddIcon from '@material-ui/icons/Add';
import CancelIcon from '@material-ui/icons/Cancel';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Search from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton';
import OpenInBrowserIcon from '@material-ui/icons/OpenInNew';


const tableIcons = {
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <EditIcon style={{color: 'darkblue'}} {...props} ref={ref} />),
  Add: forwardRef((props, ref) => <AddIcon {...props} ref={ref} />),    
  Check: forwardRef((props, ref) => <SaveIcon style={{color: 'green'}} {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <CancelIcon {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteIcon {...props} ref={ref} />),
  Cancel: forwardRef((props, ref) => <CancelIcon {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
};
  


export default class ActiveOffersTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataArr: [],            
      dataArrHistory: [],
      queryStr: '',
      searchStr: '',
      loading: true,
      showAlert: true,
      elementOutline: [1,2,3,4,5,6,7,8]
    };
    this.ref = React.createRef([]);   
    this.videoChildRef = React.createRef([]);
    
  }


    
  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    // const id = this.props.eventId.match.params.eventId;                            
    // const parentURL = `/api/events/getcontestants/${id}`;                

    this.setState({ loading: true });      
    var dataArr = new Array();    
    // const UserId = sessionStorage.getItem('userId'); 
  

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    const url = `/api/pub/getserviceoffers`;                
    
    axiosConn.get(url)
    .then(results => {
      // console.log('results ', results);

      this.setState({ 
        dataArr: results.data.data.record,
        dataArrHistory: results.data.data.record
      });          
      
      this.setState({ isLoading: false });          
    })
    .catch(function (response) {            
      console.log(response);
    });
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************
    
    handleClick = (event) => {
      console.log(event);      
    }

    render() {           
    
        return (
            <div>
            <MaterialTable
                title="Offers"                  
                  
                icons={tableIcons}
                options={{
                  search: true,
                  exportButton: false,
                  pageSize: 10
                }}
                                   
                data={this.state.dataArr}
                columns={[               
                  { title: 'Action', render: rowData => 
                  <Button variant="outlined" size={'small'} disabled={(this.props.offerSelectedId == rowData.Id)? true: false} onClick={this.props.handleOfferSelected(rowData, true)}>
                    {(this.props.offerSelectedId == rowData.Id)? 'Chosen':  'Select'}
                  </Button>
                  },
  
                  // { title: 'Status', field: 'Status', lookup: { 0: 'New', 1: 'Approved', 2: 'Processing', 3: 'Closed', 9: 'Rejected' } },      
                  { title: '',
                    field: '',
                    render: rowData => { 
                      // return (<IconButton variant="outlined" color="primary" to="/location" onClick={() => this.handleClick(rowData.Id)} ><OpenInBrowserIcon /></IconButton>)
                        return (<IconButton variant="outlined" color="primary" component={Link} to={`/offer-support-details/${rowData.Id}/request-support`} ><OpenInBrowserIcon /></IconButton>)
                      }   
                  },
                  { title: 'Name', field: 'Name', editable: 'never' },
                  { title: 'Service', field: 'Service', editable: 'never' },
                  
                  { title: 'Description', field: 'OfferDetails', initialEditValue: 'initial edit value', editable: 'never' },
            
                  { title: 'Offer Start', 
                    field: 'DateOfferStart', 
                    render: rowData => { 
                      var date = new Date(rowData.DateOfferStart);
                      return format(date, 'MM/dd/yyyy'); 
                    }, editable: 'never' 
                  },
                  { title: 'Offer End', 
                    field: 'DateOfferEnd', 
                    render: rowData => { 
                      var date = new Date(rowData.DateOfferEnd);
                      return format(date, 'MM/dd/yyyy'); 
                    }, editable: 'never' 
                  },

                  // { title: 'Category', field: 'CategoryId', type: 'numeric', editable: 'never', lookup: { 1: 'Service/Assistance', 2: 'Goods/Items/Products', 3: 'Favor/KindGesture'} },
                  
                  // { title: 'Date Posted', 
                  //   field: 'DatePosted',     
                  //   editable: 'never',   
                  //   render: rowData => { 
                  //     var date = new Date(rowData.DatePosted);
                  //     return format(date, 'MM/dd/yyyy'); 
                  //   } 
                  // },
            
                ]}
                // parentChildData={(row, rows) => rows.find(a => a.Id === row.parentId)}
                      
                // title="Offers"
                // detailPanel={rowData => {
                //   return (
                //     <OfferDetails title={rowData.Description}/>
                //   )
                // }}
              />
            </div>
        )
    }
}

