import React, {useState, useEffect} from 'react';
import { forwardRef } from 'react';
import MaterialTable from "material-table";
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import EditIcon from '@material-ui/icons/Edit';
import SaveIcon from '@material-ui/icons/Save';
import AddIcon from '@material-ui/icons/Add';
import OpenInBrowserIcon from '@material-ui/icons/OpenInBrowser';
import CancelIcon from '@material-ui/icons/Cancel';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Remove from '@material-ui/icons/Remove';
import Moment from 'moment';
import axiosConn from '../../../Global/axiosConn';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import { format } from "date-fns";
import { useHistory } from 'react-router-dom';
import AdminConnectionHistoryDialog from '../AdminConnectionsForm/AdminConnectionHistoryDialog';
import { connect, useStore } from 'react-redux';
import * as actionTypes from './../../../store/actions';

const tableIcons = {
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <EditIcon style={{color: 'darkblue'}} {...props} ref={ref} />),
    Add: forwardRef((props, ref) => <AddIcon {...props} ref={ref} />),       
    Save: forwardRef((props, ref) => <OpenInBrowserIcon {...props} ref={ref} />),    
    Check: forwardRef((props, ref) => <SaveIcon style={{color: 'green'}} {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <CancelIcon {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteIcon {...props} ref={ref} />),
    Cancel: forwardRef((props, ref) => <CancelIcon {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };


function Editable(props) {
 
  const { useState } = React;
  const [value, setValue] = React.useState(0);
  const [admins, setAdmins] = React.useState([]);
  
  console.log('Admins ', props.admins);
  Moment.locale('en');
  var dt = '2016-05-02T00:00:00';
  const connData = props.store_userConnData;

//   useEffect(() => {
//     const url = 'api/admin/getadmins';
//     axiosConn.get(url)
//     .then(function (response) {  
//       console.log(response);          
//       var adminArr = response.data.data;
//       var adminList = [];
//       adminArr.forEach(element => {
//         const sid = element.ADUserId;
//         const name = element.UserName;
//         adminList.push(sid + ':' + name);
//       });
//       setAdmins(adminList);            
//     })
//     .catch(function (response) {
//        console.log(response);
//     });
//   }, [])
  
  const [columns, setColumns] = useState([ 
    { title: 'Id', field: 'Id', filtering: false,  },       
    { title: '', field: 'Id', filtering: false,  editable: 'never' , render:  rowData => <AdminConnectionHistoryDialog id={rowData.Id}/> },       
    { title: 'Date Connected', 
      field: 'DateCreated',     
      editable: 'never',   
      filtering: false,
      render: rowData => { 
        var date = new Date(rowData.DateCreated);
        return format(date, 'MM/dd/yyyy'); 
      }
    },
    { title: 'Connection Status', field: 'Status', width: "10%", filtering: false, lookup: { 0: 'Pending', 1: 'Connected', 9: 'Closed', 19: 'Closed - Unconfirmed', 99: 'Disconnect' } },            
    { title: 'Assigned To', field: 'AssignedToId', width: "10%", lookup: props.adminList },                
    { title: 'Offer Details', field: 'OfferDetails', width: "20%", filtering: false, editable: 'never' },
    { title: 'Offer Status', field: 'OfferStatus', width: "10%",  lookup: { 0: 'New', 1: 'Approved', 99: 'Rejected', 5: 'Pending', 9: 'Closed' , 20: 'Expired' } },                
    { title: 'Request Details', field: 'RequestDetails', width: "20%", filtering: false, editable: 'never' },
    { title: 'Request Status', field: 'RequestStatus', width: "10%",  lookup: { 0: 'New', 1: 'Approved', 99: 'Rejected', 5: 'Pending', 9: 'Closed' , 20: 'Expired' } },            
    { title: 'Category', field: 'CategoryId', width: "10%", type: 'numeric', editable: 'never', lookup: { 1: 'Service/Assistance', 2: 'Goods/Items/Products', 3: 'Favor/KindGesture'} },        
  ]);

  
  const history = useHistory();
  const [data, setData] = useState(props.store_userConnData);      
  return (
    
    <MaterialTable style={{marginTop: '5px'}}
      title="User Connections"
      columns={columns}
      icons={tableIcons}
      data={data}
      options={{
        search: true,
        exportButton: false,
        grouping: false,
        filtering: true,
        pageSize:5
      }}
      editable={{
        onRowUpdate: (newData, oldData) =>
          new Promise((resolve, reject) => {
            setTimeout(() => {              
              const dataUpdate = [...data];
              const index = oldData.tableData.id;
              newData.Status = Number(newData.Status);
              oldData.AssignedToId = newData.AssignedToId
              dataUpdate[index] = newData;
              // oldData.Status = Number(newData.Status);
              // oldData.StatusNotes = Number(newData.StatusNotes);
              setData([...dataUpdate]);                                
              const url = `${process.env.REACT_APP_BASE_URL_LOCAL}/api/admin/updateconnectionstatus`;                    
              var self=this;
              axiosConn.post(url, {                
                MatchId: newData.Id,
                MatchStatus: newData.Status,                   
                OfferId: newData.Id,
                OfferStatus: newData.OfferStatus,                                   
                RequestId: newData.Id,
                RequestStatus: newData.RequestStatus,    
                AssignedToId: newData.AssignedToId               
              })
              .then(function (response) {        
                // console.log('User Conn response ', response);
                try {
                  // props.reloadData();                  
                  if(newData.Status === 1) {
                    // Use Redux to move record from User to Admin list                  
                    props.onMoveUserConnectToAdminConnectData(newData.Id);
                    // Reload data from Redux Store
                    setData(props.store_userConnData);
                  }                  
                  
                } catch (error) {
                  console.log('After Reload Called Error ', error);
                }

              }).catch(function (response) {
                //handle error
                 console.log(response);
              });
              
              resolve();
              
            }, 1000)
          }),
        }}
    />
  )
}

const mapStateToProps = state => {
  console.log('mapStateToProps ', state);
  return {    
      store_adminConnData: state.adminConnectionsData,
      store_userConnData: state.userConnectionsData
  };
};

const mapDispatchToProps = dispatch => {
  return {
      // onSaveSearch: (searchStr) => dispatch(
      //  {
      //    type: actionTypes.USERS_SAVE_SEARCH
      // }),      
      onUpdateUserConnectData: (data) => dispatch(
      {
        type: actionTypes.ADMIN_UPDATE_USER_CONNECT_RECORD, userConnectRecord: data
      }),      
      onMoveUserConnectToAdminConnectData: (id) => dispatch(
      {
        type: actionTypes.ADMIN_MOVE_USER_CONNECT_RECORD_TO_ADMIN_CONNECT_LIST, id: id
      }),      
     }
};


function AdminUserConnectionsTable(props) {  
  return (
    <div>
      {Editable(props)}
    </div>
  )
}
export default connect(mapStateToProps, mapDispatchToProps)(AdminUserConnectionsTable);