import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100vw',
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,

  },
  listroot: {
    width: '33vw',
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,

  },

  inline: {
    
    width: '10vw'
  },
}));

export default function RequestListItem(props) {
  const classes = useStyles();
  // console.log('Props ', props);

  const showServicesSection = () => {
    return (
        <List className={classes.listroot}>
        {props.data.map((item, i) => {
            return (
            <>
            <ListItem alignItems="flex-start">
                <ListItemAvatar>
                <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
                </ListItemAvatar>
                <ListItemText
                primary={item.RequestDetails}
                secondary={
                    <React.Fragment>
                    <Typography
                        noWrap={false}
                        component="span"
                        variant="body2"
                        className={classes.inline}
                        color="textPrimary"
                    >
                        {item.RequestDetails}
                    </Typography>
                    {" — I'll be in your neighborhood doing errands this…"}
                    </React.Fragment>
                }
                />
                <ListItemSecondaryAction>
                    <Button variant="outlined" color="primary">View</Button>
                </ListItemSecondaryAction>
            </ListItem>
            <Divider variant="inset" component="li" />
            </> 
            )
          })}
        </List>
    );

  }

  return (
    <Grid container 
          className={classes.root} 
          spacing={1} 
          style={{marginTop: '20px'}} 
          direction={'row'}
          justify="center"
          alignItems="center">
        <Grid item sm={4} align="center" spacing={2}>
            <Typography variant="h5" gutterBottom>
                Services/Assistance
            </Typography>        
        </Grid>      
        <Grid item sm={4} align="center" spacing={2}>
            <Typography variant="h5" gutterBottom>
                Goods/Items/Products
            </Typography>
        </Grid>      
        <Grid item sm={4} align="center" spacing={2}>
            <Typography variant="h5" gutterBottom>
                Favor/Kind Gesture
            </Typography>
        </Grid>        
        <Grid item sm={4} align="center" spacing={2}>
            {showServicesSection()}  
        </Grid>      
        <Grid item sm={4} align="center" spacing={2}>
            {showServicesSection()}  
        </Grid>      
        <Grid item sm={4} align="center" spacing={2}>
            {showServicesSection()}  
        </Grid>      
     
    </Grid>    

  );
}
