import React from 'react';
import axiosConn from '../../../Global/axiosConn';
import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { Typography } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Link, Switch } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
// import RequestOfferFormDialog from './RequestOfferFormDialog';
import Divider from '@material-ui/core/Divider';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Alert, AlertTitle } from '@material-ui/lab';
import RequestSupportForm from './RequestSupportForm';
import { getRootURL, getStatusHelper } from '../../../Helpers/GlobalFunctions';
import './Style.css';

const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
     },
     typography: {
        body1: {
          fontWeight: 600 // or 'bold'
        }
      },
    checked: {},    
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));


export default class OfferSupportDetailsForm extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            offerId: 0,
            formData: [],
            imagesData: [],
            statusText: '',
            isLoading: true

        };   

        this.handleDateTimeChange.bind(this);
    }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    this.setState({ loading: true });      

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 200);
    
    var rootURL = getRootURL();


    const offerId = this.props.match.params.id;        
    this.setState({offerId: offerId});
    const url = `/api/pub/getsingleserviceoffer/${offerId}`;                
    
    axiosConn.get(url)
    .then(results => {                
        var imgData = results.data.data.record[0].Images;
        var imageArr = [];
        
        const statusVal = Number(results.data.data.record[0].Status);
        var status = getStatusHelper(statusVal);
        console.log('results.data ', results.data.data.record[0]);
        this.setState({ formData: results.data.data.record[0], 
                        statusText: status });

        const isImages = results.data.data.record[0].Images.length == 0 ? false : true;
        if(isImages == true)  {
            imgData.forEach(img => {
                
                const path = `${rootURL}/Content/offers/${img}`;          
                imageArr.push({original: `${path}`, thumbnail: `${path}`});
                
            });

        } 
        else {
            let imageURL = process.env.PUBLIC_URL + '/images/svg/003-hammer.svg';
            let categoryText = '';
            if(results.data.data.record[0].CategoryId == 1) {
              imageURL = process.env.PUBLIC_URL + '/images/svg/services.svg';
              categoryText = 'Service/Assistance';
            } 
            if(results.data.data.record[0].CategoryId == 2) {
              imageURL = process.env.PUBLIC_URL + '/images/svg/goods.svg';
              categoryText = 'Goods/Items/Products';
            } 
            if(results.data.data.record[0].CategoryId == 3) {
              imageURL = process.env.PUBLIC_URL + '/images/svg/kind_gesture.svg';
              categoryText = 'Favor/Kind Gesture';
            } 
                
            imageArr.push({original: `${imageURL}`, thumbnail: `${imageURL}`});
        }

             
        this.setState({ imagesData: imageArr })
        setTimeout(() => {
            this.setState({ isLoading: false });              
        }, 400);
        

    })
    .catch(function (response) {            
      console.log(response);
    });
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

    handleCheckBoxChange = (event) => {
        this.setState({ IsAnonymous: event.target.value});        
    };
    
    handleChange = (prop) => (event) => {
        this.setState({ [prop]: event.target.value});        
    };
 
    handleDateTimeChange = (event) =>  {
        this.setState({[event.target.name]: event.target.value});       
    };

    handleRadioChange = (event) => {
        this.setState({IsAnonymous: event.target.value});        
    };

    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
        const returnLocation = this.props.match.params.rtnloc;        
        const returnURL = '/' + returnLocation;
        const customRenderThumb = (children) =>
            children.map((item) => {
            // console.log(item);
            return <img src={item.props.children.props.src} style={{width:'50px', maxHeight:'100px'}} />;
        });
        return (
            <div style={{ maxWidth: "100%", marginTop: "-85px", marginRight: "25px"}}>            
                <form  noValidate autoComplete="off">
                <Paper component="div" variant="outlined" style={{ padding: "45px"}}>
                    <Grid container spacing={4} >
                    
                        <Grid sm={2}  align="left">                            
                        
                        <IconButton color="primary" size="" component={Link}  to={returnURL}>                        
                        <ArrowBackIcon />
                          <Typography variant="h6">
                            Back  
                          </Typography>                       
                        </IconButton>
                        
                        </Grid>
                        
                        <Grid sm={10} className="fieldLayoutT5" align="center"></Grid>

                        <Grid sm={1} className="fieldLayoutT5" align="center"></Grid>
                        <Grid sm={5} className="fieldLayoutT15" align="center">
                            <Grid container spacing={4} >
                                <Grid sm={11}  style={{marginTop: '15px'}} >                                                                
                                    {/* <ImageCarousel dataSource={this.state.imagesData} />            */}
                                    <Carousel showThumbs={true} 
                                              autoPlay={false} 
                                              infiniteLoop={false} 
                                              showIndicators={false}
                                              showArrows={true} 
                                              dynamicHeight={false} 
                                              thumbWidth={80} 
                                              thumbHeight={100} 
                                              renderThumbs={customRenderThumb}>                    
                                              
                                        {this.state.imagesData.map((element, i) => {          
                                            return (  
                                                <div key={i}>
                                                 <img src={element.original} style={{width: '24vw', maxHeight: '85vh'}}/>                                             
                                                {/* <div key={i} style={{width: '24vw', maxHeight: '85vh'}}>                                                                                                                 
                                                    <ReactImageMagnify 
                                                    {...{
                                                        smallImage: {
                                                            alt: '',
                                                            isFluidWidth: true,
                                                            src: element.original,                                    
                                                            
                                                        },
                                                        largeImage: {
                                                            src: element.original,
                                                            width: 1426,
                                                            height: 2000
                                                        },
                                                        lensStyle: { backgroundColor: 'rgba(0,0,0,.6)' }
                                                    }}     />                                                          
                                                     */}
                                                                                                                                                    
                                                </div>    
                                            )
                                        })}      
                                    </Carousel>       
                                </Grid>
                            </Grid>                                                 
                        </Grid>
                        

                        <Grid sm={1} >
                            <Divider orientation="vertical"  />
                        </Grid>                                                    
                        <Grid sm={4} className="fieldLayoutT5">                            
                        
                            <Grid container spacing={3} className="fieldLayoutL15">
                                <Grid sm={12} align="center" className="fieldLayoutT15">                            
                                    <Alert severity="success">
                                        <AlertTitle align="left"></AlertTitle>
                                        <Typography variant="h5" gutterBottom align="left"> 
                                            {this.state.formData.Service}
                                        </Typography>                        

                                        <Typography variant="body2" gutterBottom align="left">
                                            {this.state.formData.OfferDetails}
                                        </Typography>                        
                                    </Alert>
                                </Grid>                                                        
                                

                                <Grid sm={12} align="left" className="fieldLayoutT25">                            
                                    {/* <RequestOfferFormDialog offerId={this.props.match.params.id}/> */}
                                    {!this.state.isLoading ?
                                     <RequestSupportForm offerId={this.state.offerId} CategoryId={this.state.formData.CategoryId} />
                                    : ''
                                    }
                                </Grid>
                                
                                <Grid sm={12} align="left" className="fieldLayoutT25">                            
                                    <Divider />
                                </Grid>
                                
                            </Grid>                                          
                        </Grid>    
                    </Grid>
                </Paper>
                </form>
            </div>
        );
    }
}
