import React, {Component } from 'react'
import { createStyles, makeStyles } from '@material-ui/core/styles';
import {DropzoneDialog} from 'material-ui-dropzone'
import './DropZone.css';
import { Button } from '@material-ui/core';
import axiosConn from '../../../Global/axiosConn';
import axios from 'axios';

export default class Dropzone extends Component{
  constructor(props){
    super(props);
    this.state = {
      open: false,
      files: []
    };
  }
  handleClose() {
    this.setState({
        open: false
    });
  }

  handleOpen() {
    this.setState({
        open: true,
    });
  }

  handleSave(files) {    
    this.setState({
        files: files,
        open: false
    });

    const url = `/api/admin-offer-images/addimage`;
    const formData = new FormData();
    formData.append(`offerId`, this.props.offerId);

    for (let i = 0; i < files.length; i++) {
      formData.append(`images[${i}]`, files[i])
    }

    var self=this;
    axiosConn({
      method: 'post',
      url: url,
      data: formData,
      headers: {'Content-Type': 'multipart/form-data' }
    })
    .then(function (response) {
        //handle success          
        console.log(response);
        self.props.onHandleImageAdded();
    })
    .catch(function (response) {
        //handle error
        console.log(response);
    });
  }

  render(){
    const classes = makeStyles(theme => createStyles({
      previewChip: {
        minWidth: 160,
        maxWidth: 210
      },
    }));
    return (
      <div style={{marginLeft: '52px'}}>
        <Button onClick={this.handleOpen.bind(this)}>
          Add Image
        </Button>
        <DropzoneDialog     
          open={this.state.open}
          onSave={this.handleSave.bind(this)}
          dropzoneClass="drop-zone"
          // onChange={this.props.handleChange.bind(this)}
          acceptedFiles={['image/*']}
          dropzoneText={`Upload Images (Maximum ${this.props.maxImagesAllowed})`}
          filesLimit={this.props.maxImagesAllowed}
          useChipsForPreview
          previewGridProps={{container: { spacing: 1, direction: 'row' }}}
          previewChipProps={{classes: { root: classes.previewChip } }}
          previewText="Selected files"
          />
        </div>
    )
  }
}
 
