export function getStatusHelper(statusVal) {
    var status = "";
    switch (statusVal) {        
        case 0:
            status = "New";
            break;
        case 1:
            status = "Approved";            
            break;
        case 5:
            status = "Pending";        
            break;
        case 7:
            status = "Connected";        
            break;                        
        case 9:
            status = "Closed";        
            break;                        
        case 10:
            status = "Expired";        
             break;
        case 99:
            status = "Rejected";        
            break;
        case 999:
            status = "Cencelled";        
            break;
    }
    return status;
}

export function getRootURL() {
    var rootURL = 'http://vhdweb05.ccdoa.net:8083';
    if (process.env.NODE_ENV === 'development') {      
      rootURL = process.env.REACT_APP_BASE_URL_LOCAL;
    } else {
      if(window.location.hostname === process.env.REACT_APP_PROD_DOMAIN) {
        rootURL = process.env.REACT_APP_BASE_URL_PROD;
      } else {
        rootURL = process.env.REACT_APP_BASE_URL_PROD;
      }
    }
    return rootURL;
}