import React, { useState } from 'react'
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { format } from "date-fns";
import TablePagination from '@material-ui/core/TablePagination';

const StyledTableContainer = withStyles((theme) => ({
  root: {
    maxWidth: '40vw',
    
    marginRight: '10vw',    
  },
}))(TableContainer);

const StyledTableCell = withStyles((theme) => ({
  head: {
    padding: '3px',
    fontWeight: 'bold'
  },
  body: {    
    fontSize: 12,    
  },
  
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({  
  root: {
    // '&:nth-of-type(odd)': {
    //   backgroundColor: theme.palette.action.hover,
    // },
    
  },
  
}))(TableRow);



export default function AdminConnectionStatusHistoryTable(props) {
  const { classes, onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

 
  const getFormattedDate = (newData) => {
    var date = new Date(newData);
    return format(date, 'MM/dd/yyyy hh:mm'); 
  }

  const getStatusText = (val) => {
    var statusTxt = '';
    switch (val) {
      case 0:
        statusTxt = 'Pending';
        break;
        
      case 1:
        statusTxt = 'Connected';
        break;

      case 9:
        statusTxt = 'Closed';
        break;  

      case 19:
        statusTxt = 'Closed - Unconfirmed';
        break;

      case 99:
        statusTxt = 'Disconnect';
        break;
                          
      default:
        break;
    }
    return statusTxt; 
} 
  const renderPaginatedTable = event => {  
    return (
      <div >
        <Paper >
        <TableContainer style={{maxHeight: '310px', overflowY: 'auto'}}>        
          <Table stickyHeader >
            <TableHead>
              <TableRow>
                <StyledTableCell>Status</StyledTableCell>                
                <StyledTableCell align="left">Notes</StyledTableCell>
                <StyledTableCell align="left">Recorded By</StyledTableCell>
                <StyledTableCell align="left">Date</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {props.data.map((row, i) => {              
                return (                
                <StyledTableRow  key={row.Id}>
                  <TableCell align="left">{getStatusText(row.Status)}</TableCell>
                  <TableCell align="left">{row.Notes}</TableCell>                  
                  <TableCell align="left">{row.UserName}</TableCell>
                  <TableCell align="left">
                    {getFormattedDate(row.DateCreated)}
                  </TableCell>
                </StyledTableRow>                                        
                )
              })}      
            </TableBody>  
          </Table>
        </TableContainer>
      </Paper>
      </div>
    )
  }

  const renderNoRecords = event => {
    const tableCls = {  
      marginLeft: '10vw'
    }
    return (
      <div >
        <Typography variant="h5" color="textSecondary" gutterBottom>
         No History
        </Typography>
      </div>
    )
  }

  return (
    <div >
      {props.data.length > 0? renderPaginatedTable() : renderNoRecords()}
    </div>
  )

}


