import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";
import { Alert } from 'react-bootstrap';
import ImageCard from './ImageCard/ImageCard';
import Button from '@material-ui/core/Button';
import VideoDialog from './VideoDialog';
import OfferCategories from './OfferCategories';
import axiosConn from '../../Global/axiosConn';
import SearchBar from "material-ui-search-bar";
import RequestListItem from './RequestListItem';

const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(1),
  },
  input: {
      width: '80vw'
  },

}));

export default class  MasonryLayout extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchOffersListOriginal: [],            
      searchOffersList: [],            
      searchRequestsListOriginal: [],            
      searchRequestsList: [],            
      videoArr: [],
      votingHistory: [],
      sliderValue: 10,
      chosenContestant: null,
      chosenCategory: 0,
      queryStr: '',
      searchStr: '',
      currentOffers: [],
      loading: true,
      showAlert: true,
      votingDict: [],
      text: 'Connect with Kindness',
      elementOutline: [1,2,3,4,5,6,7,8]
    };
    this.ref = React.createRef([]);   
    this.videoChildRef = React.createRef([]);
  }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    // const id = this.props.eventId.match.params.eventId;                            
    // const parentURL = `/api/events/getcontestants/${id}`;                

    this.setState({ loading: true });      
    var dataArr = new Array();    
    // const UserId = sessionStorage.getItem('userId'); 
  

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    const url = `/api/pub/getallrecordslandingpage`;                
    
    axiosConn.get(url)
    .then(results => {
      
      this.setState({ 
        searchOffersList: results.data.offers.record,
        searchOffersListOriginal: results.data.offers.record,
        searchRequestsList:  results.data.requests,
        searchRequestsListOriginal: results.data.requests
      });          

      this.setState({ isLoading: false });          
    })
    .catch(function (response) {            
      console.log(response);
    });    

  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

  handleSearch = (event) => {
    
    const searchTerm = this.state.searchStr;
    var newArr = [];
    var counter = 0;
    this.state.searchOffersListOriginal.forEach(item => {
   
      if(item.Service.toLowerCase().indexOf(searchTerm.toLowerCase()) > 0 || item.Service.toLowerCase().startsWith(searchTerm.toLowerCase())) {
        newArr.push(item);
        counter = counter + 1;
      }       
    });
    
    this.setState({searchOffersList: newArr});

  }

  handleChange = (event) => {    
    this.setState({searchStr: event.target.value});    
  }

  handleResetSearch = (event) => {
    this.setState({searchOffersList: this.state.searchOffersListOriginal});
    this.setState({searchStr: ''})
  }

  triggerClassChildAlert = () => {    
    this.VideoCard.showAlert();        
  }

  
  //***********************************************************************************************************************************
  //*  L I K E S   -   N O T   U S E D
  //***********************************************************************************************************************************
  
  handleClickedCategory = (event) => {
    
    let cat = event.target.textContent;
    let chosenCat = 0;
    switch(cat) {
      case 'All': chosenCat = 0; break;
      case 'Service/Assistance': chosenCat = 1; break;
      case 'Goods/Items/Products': chosenCat = 2; break;
      case 'Favor/Kind Gesture': chosenCat = 3; break;
    }

    this.setState({chosenCategory: chosenCat});
    
    var newArr = [];
    var counter = 0;
    if(chosenCat != 0) {
      this.state.searchOffersListOriginal.forEach(item => {   
        if(item.CategoryId === chosenCat) {
          newArr.push(item);
          counter = counter + 1;
        }       
      });
      
      this.setState({searchOffersList: newArr});      
    }
    else {
      this.setState({searchOffersList: this.state.searchOffersListOriginal});      
    }
  }

  //***********************************************************************************************************************************
  //*  L I K E S   -   N O T   U S E D
  //***********************************************************************************************************************************

  handleLikeEvent = (id) => {
        
    var liked = true;
    var dataArr = this.state.searchOffersListOriginal;
    dataArr.forEach(item => {     
      console.log(item);
      if(item.Id === id && item.LikedId > 0) {       
        liked = false;
      }             
    });
        
    if(liked) {

      const url = `${process.env.REACT_APP_BASE_URL_LOCAL}/api/likes/add/${id}`;
  
      axiosConn.get(url)
      .then(results => {
        const likedId = results.data.LikedId;
        var newArr = [];
        dataArr.forEach(item => {     
          if(item.Id === id) {
            item.LikedId = likedId;
          }       
          newArr.push(item);    
        });
        this.setState({searchOffersList: newArr});      
      })
      .catch(function (response) {            
        console.log(response);
      });
    }
    else {

      const url = `${process.env.REACT_APP_BASE_URL_LOCAL}/api/likes/remove/${id}`;
      
      axiosConn.delete(url)
      .then(results => {      
        var newArr = [];  
        dataArr.forEach(item => {     
          if(item.Id === id) {
            item.LikedId = 0;
          }       
          newArr.push(item);    
        });
        this.setState({searchOffersList: newArr});      
      })
      .catch(function (response) {            
        console.log(response);
      });
    }    
  };

  
  //***********************************************************************************************************************************
  //*  S K E L E T O N   -   L O A D I N G
  //***********************************************************************************************************************************
  
  renderSkeletonSection = () => {
    return (            
      <Grid             
        container item
        direction="row"
        justify="left"
        alignItems="center"
        xs={12} md={12} lg={12} spacing={1}>
        {this.state.elementOutline.map((element, i) => {
          return (
            <Grid item xs={12} md={6} lg={3}>
              <SkeletonTheme color="#E0E0E0" highlightColor="#A0A0A0">
              <div className="row">
                <div className="col-4 text-left">
                  <Skeleton circle={true} height={50} width={50} />
                </div>
                <div className="col-8 text-left">
                  <Skeleton count={1} height={25} className="mt-3"/>
                </div>
              </div>
              
                <p>
                
                <Skeleton count={2} height={25}/>
                <Skeleton count={1} height={200}/>
                <Skeleton count={2} height={25}/>

                </p>
              </SkeletonTheme>;
            </Grid>
            )
          })}
      </Grid>           
    )
  }

  
  //***********************************************************************************************************************************
  //*  O F F E R S   S E C T I O N 
  //***********************************************************************************************************************************
  
  renderOffersSection = (mobile) => {
    
    return (    
      
      <Grid           
        container item        
        direction="row"        
        justify={mobile ? 'center' : 'left'}
        alignItems={mobile ? 'center' : 'left'}
        xs={12} md={12} lg={12} spacing={1} >
        {this.state.searchOffersList.map((element, i) => {          
          return (  
            <Grid item xs={12} md={6} lg={2} >                
              <ImageCard  data={element} 
                          onChangeVote={this.changeVote} 
                          handleLikeEvent={this.handleLikeEvent}
                          likedId={element.LikedId} 
                          title={element.Service}                           
                          contestantId={element.Id}                           
                          voteId={this.state.chosenContestant}/>
            </Grid>
          )
        })}
      </Grid>  
    )
  }

  
  //***********************************************************************************************************************************
  //* R E Q U E S T S   S E C T I O N
  //***********************************************************************************************************************************
  
  renderRequestsSection = (mobile) => {
    
    return (    
      <>
        <RequestListItem data={this.state.searchRequestsList}/>
      </>
    )
  }
 
  AlertDismissible = () => {      
    if (this.state.showAlert === true) {
      return (
        <Alert variant="info" onClose={() => this.setState({showAlert: false})} dismissible>  
          <p className="alert-msg">
              Please review each video below and vote for your favorite. Note, you can only vote once. Voting closes on 2/9.
          </p>
        </Alert>
      );
    }    
  }

  render() {            
    
    const data = this.state.contestants;
    const categoryArr = ['Clothing', 'Computer', 'Electrical', 'Furniture', 'Painting', 'Plumbing', 'Remodeling'];
    return (
      <React.Fragment>
        {/* <Typography variant="h3" gutterBottom>
        Video Section
        </Typography>        */}

        
      {/* <h1 className="title">Offers </h1>       */}
      <Grid container className={classes.root} spacing={1} style={{marginTop: '-70px'}}>
        <Grid item xs={2} align="center">
          <VideoDialog />        
        </Grid>
        
        <Grid item xs={4} align="right">
          <OfferCategories handleClickedCategory={this.handleClickedCategory} value={this.state.chosenCategory}/>
        </Grid>
          
        <Grid item xs={3} align="right">
          <SearchBar
              value={this.state.searchStr}
              onChange={(newValue) => this.setState({ searchStr: newValue })}
              onRequestSearch={() => this.handleSearch()}
              onCancelSearch={() => this.handleResetSearch()}
              placeholder="Search Offers"
            />                     
        </Grid>
        <Grid item xs={1} align="right">
          <Button variant="outlined" color="primary" size="small" style={{marginLeft: '-27px', marginTop: '4px', height: '44px'}} onClick={this.handleResetSearch}>Reset All</Button>
        </Grid>

      </Grid>
                  
      <div className="media-wrapper" >          
         {this.renderOffersSection(false)}
         {/* {this.renderRequestsSection(false)} */}
      </div>        
               
      </React.Fragment>
    );
  }
}
