import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import ReactPlayer from 'react-player'

export default function VideoDialog() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="outlined" color="primary" size="large" style={{height: '50px'}} onClick={handleClickOpen}>
        Watch Video
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        maxWidth={800}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"About Connect with Kindness"}</DialogTitle>
        <DialogContent>
          <ReactPlayer  playing controls 
            className='react-player'
            url='https://www.youtube.com/watch?v=eZujoh1Yn7c'
            playing={false} />

        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClose} color="primary" >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}