import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import axiosConn from '../../Global/axiosConn';
import AdminTabsLayout from './AdminTabsLayout';
import { connect, useStore } from 'react-redux';
import * as actionTypes from './../../store/actions';
const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    
  },
}));

class AdminLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      offersDataArr: [],            
      offersDataArrHistory: [],
      requestsDataArr: [],            
      requestsDataArrHistory: [],
      adminConnectionsArr: [],            
      userConnecionsArr: [],            
      successfulConnectionsArr: [],            
      queryStr: '',
      searchStr: '',
      chosenCategory: 100,
      loading: true,
      showAlert: true,
      elementOutline: [1,2,3,4,5,6,7,8],
      initTabVal: Number(this.props.match.params.tabid)
    };

    this.ref = React.createRef([]);   
    this.videoChildRef = React.createRef([]);    
  }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    this.setState({ loading: true });      
    var dataArr = new Array();    
    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    const url = `/api/admin/getall`;                
    
    axiosConn.get(url)
    .then(results => {      
      this.setState({         
        offersDataArr: results.data.offers,
        offersDataArrHistory: results.data.offers,
        requestsDataArr: results.data.requests,
        requestsDataArrHstory: results.data.requests,
        adminConnectionsArr: results.data.adminConnections,            
        userConnecionsArr: results.data.userConnecions,            
        successfulConnectionsArr: results.data.successfulConnections,              
        isLoading: false,        
      });          
      this.props.onSaveUserConnectData(results.data.userConnecions);      
      this.props.onSaveAdminConnectData(results.data.adminConnections);      
      this.props.onSaveSuccessfulConnectData(results.data.successfulConnections);      

      console.log('store_adminConnData ', this.props.store_adminConnData)
      console.log('store_userConnData ', this.props.store_userConnData)
    })
    .catch(function (response) {            
      console.log(response);
    });
  }


  reloadData() {
    
    console.log('Parent Reload Data Called');
    // this.setState({ loading: true });      
    var dataArr = new Array();    
    // setTimeout(() => {        
    //   this.setState({ loading: false });          
    // }, 2000);
    
    const url = `/api/admin/getall`;                
    setTimeout(() => {
      var self = this;
      axiosConn.get(url)
      .then(results => {
        // console.log('results ', results);
  
        self.setState({         
          offersDataArr: results.data.offers,
          offersDataArrHistory: results.data.offers,
          requestsDataArr: results.data.requests,
          requestsDataArrHstory: results.data.requests,        
          adminConnectionsArr: results.data.adminConnections,            
          userConnecionsArr: results.data.userConnecions,            
          successfulConnectionsArr: results.data.successfulConnections,            
  
          isLoading: false,        
          isReloadConnectionsData: false,        
        });          
              
      })
      .catch(function (response) {            
        console.log(response);
      });
        
    }, 1000);
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

  handleClickedCategory = (event) => {
    
    let cat = event.target.textContent;
    let chosenCat = 100;

    switch(cat) {
      case 'All': chosenCat = 100; break;
      case 'New': chosenCat = 0; break;
      
      case 'Approved': chosenCat = 1; break;
      
      case 'Connected By User': chosenCat = 2; break;

      case 'Processing': chosenCat = 5; break;
                  
      case 'Closed': chosenCat = 9; break;      

      case 'Expired': chosenCat = 20; break;      

      case 'Rejected': chosenCat = 99; break;
    }

    this.setState({chosenCategory: chosenCat});
    
    var newArr = [];
    var counter = 0;
    if(chosenCat != 100) {
      this.state.offersDataArr.forEach(item => {   
        if(item.Status === chosenCat) {
          newArr.push(item);
          counter = counter + 1;
        }       
      });
      
      this.setState({offersDataArr: newArr});      
      setTimeout(() => {
        console.log('this.state.dataArr ', this.state.dataArr);
      }, 200);
    }
    else {
      this.setState({offersDataArr: this.state.offersDataArrHistory});      
      setTimeout(() => {
        console.log('this.state.dataArr ', this.state.offersDataArr);
      }, 200);
    }
  }


  TopRow() {
    return (
      <React.Fragment>        
        <Grid container spacing={3} >           
          <Grid item xs={12} spacing={5} >
            <AdminTabsLayout initTabVal={this.state.initTabVal} 
                             offersDataArr={this.state.offersDataArr} 
                             requestsDataArr={this.state.requestsDataArr}
                             isReloadConnectionsData={this.state.isReloadConnectionsData}
                             adminConnectionsData= {this.state.adminConnectionsArr}            
                             userConnecionsData= {this.state.userConnecionsArr}            
                             successfulConnectionsData= {this.state.successfulConnectionsArr}                     
                             reloadData={this.reloadData}/>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  BottomRow() {
      return (
        <React.Fragment>
          <Grid container spacing={3} >
            <Grid item xs={4}>
                <Paper className={classes.paper}>
                </Paper>
            </Grid>
                {/* <Grid item xs={6}   >
                    <Paper className={classes.paper} >
                        <AdminChart />
                    </Paper>
                </Grid> */}
        </Grid>
        </React.Fragment>
      );
    }

  render() {
      return (
          <div>
              <Grid container spacing={1}>
                  <Grid container item xs={12} spacing={3}>
                      {this.TopRow()}
                  </Grid>
              </Grid>
                  
          </div>
      )
  }
}


const mapStateToProps = state => {
  console.log('mapStateToProps ', state);
  return {    
      store_adminConnData: state.adminConnectionsData,
      store_userConnData: state.userConnectionsData
  };
};

const mapDispatchToProps = dispatch => {
  return {
      // onSaveSearch: (searchStr) => dispatch(
      //  {
      //    type: actionTypes.USERS_SAVE_SEARCH
      // }),      
      onSaveUserConnectData: (data) => dispatch(
      {
          type: actionTypes.ADMIN_SAVE_USER_CONNECT_DATA, userConnectData: data
      }),      
      onSaveAdminConnectData: (data) => dispatch(
      {
          type: actionTypes.ADMIN_SAVE_ADMIN_CONNECT_DATA, adminConnectData: data
      }),      

      onSaveSuccessfulConnectData: (data) => dispatch(
      {
          type: actionTypes.ADMIN_SAVE_SUCCESSFUL_CONNECT_DATA, successConnectData: data
      }),      
     }
};

export default connect(mapStateToProps, mapDispatchToProps)(AdminLayout);