import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import DoneIcon from '@material-ui/icons/Done';

const classes = makeStyles((theme) => ({
    root: {
        position: 'absolute',
        top: '400px',
        flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',      
      color: theme.palette.text.secondary,      
      margin: `${theme.spacing(1)}px auto`,
      
    },
    chip: {
        margin: theme.spacing(0.5),
        color: 'green'
    },
    appBar: {
        position: 'fixed',
        top: '100px'
    }
}));


export default function OfferCategories(props) {    
    const chosenCat = props.value;
    
    return (
        <div className={classes.root}>
            <Grid container spacing={3} style={{marginTop: '-5px'}}>
                <Grid item xs={12} align="left">
                        <Chip color={chosenCat == 0 ? 'primary' : ''} onClick={props.handleClickedCategory} label={'All'} className={classes.chip} icon={chosenCat == 0 ? <DoneIcon /> : ''} />
                        <Chip color={chosenCat == 1 ? 'primary' : ''} onClick={props.handleClickedCategory} label={'Service/Assistance'} className={classes.chip} icon={chosenCat == 1 ? <DoneIcon /> : ''}/>                            
                        <Chip color={chosenCat == 2 ? 'primary' : ''} onClick={props.handleClickedCategory} label={'Goods/Items/Products'} className={classes.chip} icon={chosenCat == 2 ? <DoneIcon /> : ''}/>
                        <Chip color={chosenCat == 3 ? 'primary' : ''} onClick={props.handleClickedCategory} label={'Favor/Kind Gesture'} className={classes.chip} icon={chosenCat == 3 ? <DoneIcon /> : ''}/>                            
                        {/* <Chip  onClick={handleClick} label={'Clothing'} className={classes.chip}/>                        
                        <Chip  variant="outlined" color="primary" onClick={handleClick} label={'More..'} className={classes.chip}/>       */}
                </Grid>            
            </Grid>
        </div>
    )

}
