import React, { Component } from 'react'
import axiosConn from '../../../Global/axiosConn';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { format } from "date-fns";
import TablePagination from '@material-ui/core/TablePagination';

const StyledTableContainer = withStyles((theme) => ({
  root: {
    maxWidth: '40vw',    
    marginRight: '10vw',    
  },
}))(TableContainer);

const StyledTableCell = withStyles((theme) => ({
  head: {
    padding: '3px',
    fontWeight: 'bold'
  },
  body: {    
    fontSize: 12,    
  },
  
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({  
  root: {
    // '&:nth-of-type(odd)': {
    //   backgroundColor: theme.palette.action.hover,
    // },
    
  },
  
}))(TableRow);

export default class AdminRequestStatusHistoryDetailSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      historyListOriginal: [],            
      historyList: [],            
      elementOutline: [1,2,3,4,5,6,7,8],
      rowsPerPage: 3,
      page: 0
    };

    this.ref = React.createRef([]);   
    this.videoChildRef = React.createRef([]);
   
  }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {
    this.onLoadData();
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

  onLoadData = (event) => {
    const id = this.props.id;                            
           
    this.setState({ loading: true });      
    var dataArr = new Array();    
    // const UserId = sessionStorage.getItem('userId'); 
  

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    const url = `/api/admin-requests/getstatushistory/${id}`;                 
    axiosConn.get(url)
    .then(results => {
       console.log('Admin Detail Results ', results);
      
      this.setState({ 
        historyList: results.data.data,
        historyListOriginal: results.data.data
      });          
      
      this.setState({ isLoading: false });          
    })
    .catch(function (response) {            
      console.log(response);
    });    
  }

  getFormattedDate = (newData) => {
    var date = new Date(newData);
    return format(date, 'MM/dd/yyyy hh:mm'); 
  }

  getStatusText = (val) => {
    var statusTxt = '';
    switch (val) {
      case 0:
        statusTxt = 'New';
        break;
        
      case 1:
        statusTxt = 'Approved';
        break;

      case 2:
        statusTxt = 'Connected By User';
        break;
  
      case 5:
        statusTxt = 'Processing';
        break;

      case 9:
        statusTxt = 'Closed';
        break;

      case 20:
        statusTxt = 'Expired';
        break;

      case 99:
        statusTxt = 'Rejected';
        break;
                          
      default:
        break;
    }
    return statusTxt; 

  }

  handleChangePage = (event, newPage) => {
    this.setState({page: newPage});    
  };

  handleChangeRowsPerPage = (event) => {    
    this.setState({rowsPerPage: +event.target.value});
    this.setState({page: 0});    
  };


  renderTable() {
    const tableCls = {  
      marginLeft: '1vw',
      marginRight: '1vw'
    }
    const container = {
      maxHeight: 40,
    }
    return (
      <div >
        <TableContainer  style={{height: '40px'}}>
          <Table stickyHeader >
            <TableHead>
              <TableRow>
                <StyledTableCell>Status</StyledTableCell>                
                <StyledTableCell align="left">Notes</StyledTableCell>
                <StyledTableCell align="left">Recorded By</StyledTableCell>
                <StyledTableCell align="left">Date</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.state.historyList.map((row, i) => {     
                return (                
                <StyledTableRow  key={row.Id}>
                  <TableCell align="left">{this.getStatusText(row.Status)}</TableCell>
                  <TableCell align="left">{row.Notes}</TableCell>                  
                  <TableCell align="left">{row.UserName}</TableCell>
                  <TableCell align="left">
                    {this.getFormattedDate(row.DateCreated)}
                  </TableCell>
                </StyledTableRow>                                        
                )
              })}      
            </TableBody>  
          </Table>
        </TableContainer>
      </div>
    )
  }

  renderNoRecords() {
    const tableCls = {  
      marginLeft: '10vw'
    }
    return (
      <div >
        <Typography variant="h5" color="textSecondary" gutterBottom>
         No History
        </Typography>
      </div>
    )
  }

  renderTable() {  
    return (
      <div >
        <Paper >
        <TableContainer style={{maxHeight: '210px'}}>        
          <Table stickyHeader >
            <TableHead>
              <TableRow>
                <StyledTableCell>Status</StyledTableCell>                
                <StyledTableCell align="left">Notes</StyledTableCell>
                <StyledTableCell align="left">Recorded By</StyledTableCell>
                <StyledTableCell align="left">Date</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.state.historyList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((row, i) => {              
                return (                
                <StyledTableRow  key={row.Id}>
                  <TableCell align="left">{this.getStatusText(row.Status)}</TableCell>
                  <TableCell align="left">{row.Notes}</TableCell>                  
                  <TableCell align="left">{row.UserName}</TableCell>
                  <TableCell align="left">
                    {this.getFormattedDate(row.DateCreated)}
                  </TableCell>
                </StyledTableRow>                                        
                )
              })}      
            </TableBody>  
          </Table>
        </TableContainer>
        <TablePagination
        rowsPerPageOptions={[3, 5, 10]}
        component="div"
        count={this.state.historyList.length}
        rowsPerPage={this.state.rowsPerPage}
        page={this.state.page}
        onChangePage={this.handleChangePage}
        onChangeRowsPerPage={this.handleChangeRowsPerPage}
      />
      </Paper>
      </div>
    )
  }

  //***********************************************************************************************************************************
  //*  R E N D E R
  //***********************************************************************************************************************************

  render() {  
    return (
      <div >
        {this.state.historyList.length > 0? this.renderTable() : this.renderNoRecords()}
      </div>
    )
  }
}
