import React from 'react';
import axiosConn from '../../../Global/axiosConn';
import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import "react-responsive-carousel/lib/styles/carousel.min.css";

import AdminRequestStatusHistoryDetailSection from './AdminRequestStatusHistoryDetailSection';
// import AdminStatusNotesHistoryTable from './AdminStatusNotesHistoryTable';
import RequestOfferConnectsTable from './RequestOfferConnectsTable';
import { Link } from 'react-router-dom';
import { Typography } from '@material-ui/core';
import { getStatusHelper } from '../../../Helpers/GlobalFunctions';

const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
     },
     typography: {
        body1: {
          fontWeight: 600 // or 'bold'
        }
      },
    checked: {},    
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

export default class AdminRequestDetailsForm extends React.Component {
    constructor(props) {
        super(props);
        this._child = React.createRef();
        this.state = {
            offerId: 0,
            formData: [],
            connData: [],
            imagesData: [],
            statusOption: 0,
            statusText: '',
            offersDataArr: [],
            offersDataArrHistory: [],
            connectionsDataArr: [],
            connectionsDataArrHistory: []
        };   

        this.listRef = React.createRef();

        this.handleDateTimeChange.bind(this);
    }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {
    // const id = this.props.eventId.match.params.eventId;                            
    // const parentURL = `/api/events/getcontestants/${id}`;                    
    this.onLoadData();  
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************
   
    onLoadData = (event) => {

        this.setState({ loading: true });      
        var dataArr = new Array();    
        // const UserId = sessionStorage.getItem('userId'); 
          
        setTimeout(() => {        
          this.setState({ loading: false });          
        }, 2000);
                    
        const offerId = this.props.match.params.id;            
        const url = `/api/admin-requests/getsingleservicerequest/${offerId}`;                
        
        axiosConn.get(url)
        .then(results => {        
            // console.log('Request Details ', results.data);
            
            var imageArr = [];
            
            const statusVal = Number(results.data.data.Status);
            var status = getStatusHelper(statusVal);            
            console.log('formData ', results.data.data.record);
            this.setState({ formData: results.data.data, 
                            statusText: status, 
                            connData: results.data.connData, 
                            statusOption: statusVal});
            this.setState({ isLoading: false });          
    
        })
        .catch(function (response) {            
          console.log(response);
        });

        const offersUrl = `/api/admin/getallconnected`;                    
        axiosConn.get(offersUrl)
        .then(results => {
        //   console.log('results ', results);
    
          this.setState({ 
            offersDataArr: results.data.offers,
            offersDataArrHistory: results.data.offers,
            connectionsDataArr: results.data.connections,
            connectionsDataArrHistory: results.data.connections
          });          
          
          this.setState({ isLoading: false });          
        })
        .catch(function (response) {            
          console.log(response);
        });

        setTimeout(() => {
            console.log('this.state.connectionsDataArr ', this.state.connectionsDataArr);
        }, 300);
    }

    

    // *********************************************************************************************************
    // * Update Status and add Notes
    // *********************************************************************************************************

    onUpdateStatus = (event) => {        
        const url = `/api/admin-requests/addstatusnotes`;                    
        const userID = Number(sessionStorage.getItem('UserId'))
        var self=this;
        axiosConn.post(url, {
            RecordId: this.props.match.params.id,
            UserId: userID,
            Status: this.state.statusOption,
            Notes: this.state.notes
        })
        .then(function (response) {
            console.log(response);                                
            self.onLoadData();
        }).catch(function (response) {
          //handle error
           console.log(response);
        });    

        setTimeout(() => {            
            this._child.current.onLoadData();
        }, 200);
    }

    onUpdateNotes = (event) => {        
        this.setState({notes: event.target.value});
    }

    handleStatusChange = (event) => {        
        this.setState({statusOption: event.target.value});
    }


    handleCheckBoxChange = (event) => {
        this.setState({ IsAnonymous: event.target.value});        
    }
    
    handleChange = (prop) => (event) => {
        this.setState({ [prop]: event.target.value});        
    }
 
    handleDateTimeChange = (event) =>  {
        this.setState({[event.target.name]: event.target.value});       
    }

    handleRadioChange = (event) => {
        this.setState({IsAnonymous: event.target.value});        
    }

    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
        const returnLocation = this.props.match.params.rtnloc;        
        const returnTabId = this.props.match.params.tabid;
        const returnURL = '/' + returnLocation + '/' + returnTabId;
        
        const getStatusColor = (status) => {
            var statusColor = 'darkgreen';
            switch(status) {
                case 0: statusColor = 'barkblue'; break;
                case 1: statusColor = 'darkgreen'; break;
                case 5: statusColor = 'darkgreen'; break;
                case 7: statusColor = 'darkgreen'; break;
                case 9: statusColor = 'black'; break;
                case 10: statusColor = 'black'; break;
                case 99: statusColor = 'darkred'; break;
            }

            return statusColor;
        }

        return (
            <div style={{ maxWidth: "100%", marginTop: "-85px", marginRight: "25px"}}>            
                <form  noValidate autoComplete="off">                               

                    <Grid container spacing={4} >                    
                        <Grid sm={2}  align="left">                            
                        
                            <IconButton color="primary" size="" component={Link}  to={returnURL}>                        

                            <ArrowBackIcon />
                                <Typography variant="h6">
                                    Back  
                                </Typography>                       
                            </IconButton>
                            
                        </Grid>

                        <Grid sm={8}  align="center">                            
                            <Typography variant="h4" gutterBottom>
                            Admin - Request Form
                            </Typography>
                        </Grid>
                        <Grid sm={12}  align="center" spacing={5}>                            
                            <Typography variant="h4" gutterBottom>
                                <Divider />
                            </Typography>
                        </Grid>

                        <Grid sm={10} style={{marginTop: '5px'}} align="center"></Grid>

                        
                        <Grid sm={6} style={{marginTop: '45px'}} align="center">
                            <Grid container spacing={1} >
                                <Grid sm={12}  style={{marginTop: '15px'}} >                                                                              
                                    <RequestOfferConnectsTable data={this.state.offersDataArr} 
                                                              connectionsData={this.state.connectionsDataArr} 
                                                              requestTitle={this.state.formData.RequestDetails} 
                                                              parentId={this.props.match.params.id} />
                                </Grid>
                            </Grid>                                                 
                        </Grid>


                        <Grid sm={1} style={{marginTop: '5px'}} align="center"></Grid>


                        <Grid sm={5} style={{marginTop: '5px'}} >                            
                        
                            <Grid  spacing={3} style={{marginLeft: '15px'}} >

                                <Paper style={{padding: '20px'}}>
                                    
                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="h5" gutterBottom align="left"> 
                                     Request
                                    </Typography>                        
                                </Grid>                                                        
                                {this.state.connData.length != 0 ?                          
                                    <>
                                    <Grid sm={12}  align="center" spacing={5}>                            
                                        <Typography variant="h4" gutterBottom>
                                            <Divider />
                                        </Typography>
                                    </Grid>
                                    <Grid sm={12}  align="center" spacing={5}>                            
                                        <Typography variant="h6" align="left" gutterBottom>
                                            Connected to {this.state.connData[0].OfferService}
                                        </Typography>
                                    </Grid>

                                    </>

                                : ''}


                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>
                                
                                <Grid sm={12} align="center" style={{marginTop: '15px'}} >                            
                                    <Typography variant="h6" gutterBottom align="left">
                                    <b>Details:</b> {this.state.formData.RequestDetails}
                                    </Typography>                        
                                </Grid>                                                        

                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left"> 
                                     <b>Status:</b> {this.state.statusText}
                                    </Typography>                        
                                </Grid>                                                        
                                
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>

                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Grid  container>
                                        <Grid sm={3} item align="left" >                                           
                                            <FormControl variant="outlined" size={'small'}>
                                                <InputLabel id="status">Status</InputLabel>
                                                <Select
                                                labelId="status"
                                                id="status"
                                                
                                                value={this.state.statusOption}
                                                onChange={this.handleStatusChange}
                                                label="Age"
                                                >
                                                <MenuItem value="">
                                                    <em>None</em>
                                                </MenuItem>                                        
                                                <MenuItem value={0}>New</MenuItem>
                                                <MenuItem value={1}>Approved</MenuItem>
                                                <MenuItem value={99}>Rejected</MenuItem>
                                                <MenuItem value={5}>Pending</MenuItem>
                                                <MenuItem value={7}>Connected</MenuItem>
                                                <MenuItem value={9}>Closed</MenuItem>
                                                <MenuItem value={10}>Expired</MenuItem>

                                                </Select>
                                            </FormControl>
                                        </Grid>

                                        <Grid sm={5} item  align="center" >                                           
                                            <TextField id="notes" 
                                                       name="notes"
                                                       rows={4} 
                                                       fullWidth
                                                       multiline
                                                       value={this.state.notes}
                                                       onChange={this.onUpdateNotes}
                                                       size={'small'} 
                                                       label="Notes" 
                                                       variant="outlined" />
                                        </Grid>

                                        <Grid sm={2} item  align="center" >                                           
                                            <Fab color="primary" aria-label="add" onClick={this.onUpdateStatus}>
                                                <AddIcon />
                                            </Fab>         
                                        </Grid>
                                    </Grid>
                                </Grid>
                                </Paper>
                                <Grid sm={12} align="center" item style={{marginTop: '25px'}}>                                           
                                    <AdminRequestStatusHistoryDetailSection id={this.props.match.params.id} ref={this._child} />                                    
                                </Grid>

                            </Grid>   
                        
                        </Grid>
    
                    </Grid>
                
                </form>
            </div>
        );
    }
}

