import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';

const root = {
    flexGrow: 1,
}

const control = {
    padding: '20px'
}

const paper = {
    height: 140,
    width: 100,
}

export default class ShoppingCartParentLayout extends Component {
  
    render() {
        return (
            <div style={{marginTop: '20px'}}>
                <Grid container style={root} spacing={2}>
                    <Grid item xs={12}>
                        <Grid container justify="center" spacing={3}>
                            <Grid key={1} item>
                                <Paper style={paper}>
                                    asd
                                </Paper>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className={control}>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        )
    }
}
