import React from 'react';
import axiosConn from '../../../Global/axiosConn';
import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import { Carousel } from 'react-responsive-carousel';
import AdminOfferStatusHistoryDetailSection from './AdminOfferStatusHistoryDetailSection';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './OfferSupportDetailsForm.css';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import { Link } from 'react-router-dom';
import { Typography } from '@material-ui/core';
import { getRootURL, getStatusHelper } from '../../../Helpers/GlobalFunctions';
import ImageGalleryQuiltEditable from './ImageGalleryQuiltEditable';


const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
     },
     typography: {
        body1: {
          fontWeight: 600 // or 'bold'
        }
      },
    checked: {},    
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));


export default class AdminOfferDetailsForm extends React.Component {
    constructor(props) {
        super(props);
        this._child = React.createRef();
        this.state = {
            offerId: 0,
            formData: [],
            imagesData: [],
            imagesList: [],
            statusOption: 0,
            statusText: '',
        };   

        this.listRef = React.createRef();

        this.handleDateTimeChange.bind(this);
    }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {
    this.onLoadData();  
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************
 
    onLoadData = (event) => {

        this.setState({ loading: true });      
        var dataArr = new Array();    
        // const UserId = sessionStorage.getItem('userId'); 
          
        setTimeout(() => {        
          this.setState({ loading: false });          
        }, 2000);
        
        var rootURL = getRootURL();
 
        const offerId = this.props.match.params.id;        
        
        const url = `/api/admin-offers/getsingleserviceoffer/${offerId}`;                
        
        axiosConn.get(url)
        .then(results => {                    
            var imgData = results.data.data.record[0].Images;
            var imageArr = [];
            const statusVal = Number(results.data.data.record[0].Status);
            // Get status from helper functer
            var status = getStatusHelper(statusVal);
                                    

            this.setState({ formData: results.data.data.record[0], 
                            imagesList: imgData, 
                            statusText: status, 
                            statusOption: statusVal});
            
            const isImages = results.data.data.record[0].Images.length == 0 ? false : true;
            if(isImages == true)  {
                imgData.forEach(img => {
                    
                    const path = `${rootURL}/Content/offers/${img}`;          
                    imageArr.push({original: `${path}`, thumbnail: `${path}`});
                    
                });

            } 
            else {
                let imageURL = process.env.PUBLIC_URL + '/images/svg/003-hammer.svg';
                let categoryText = '';
                if(results.data.data.record[0].CategoryId == 1) {
                  imageURL = process.env.PUBLIC_URL + '/images/svg/services.svg';
                  categoryText = 'Service/Assistance';
                } 
                if(results.data.data.record[0].CategoryId == 2) {
                  imageURL = process.env.PUBLIC_URL + '/images/svg/goods.svg';
                  categoryText = 'Goods/Items/Products';
                } 
                if(results.data.data.record[0].CategoryId == 3) {
                  imageURL = process.env.PUBLIC_URL + '/images/svg/kind_gesture.svg';
                  categoryText = 'Favor/Kind Gesture';
                } 
                    
                imageArr.push({original: `${imageURL}`, thumbnail: `${imageURL}`});
            }
    
            this.setState({ imagesData: imageArr })
    
            this.setState({ isLoading: false });          
    
        })
        .catch(function (response) {            
          console.log(response);
        });
    }

    // *********************************************************************************************************
    // * Update Status and add Notes
    // *********************************************************************************************************

    onUpdateStatus = (event) => {
        const url = `/api/admin-offers/addstatusnotes`;                            
        const userID = Number(sessionStorage.getItem('UserId'))
        var self=this;
        axiosConn.post(url, {
            RecordId: this.props.match.params.id,
            UserId: userID,
            status: this.state.statusOption,
            notes: this.state.notes
        })
        .then(function (response) {
            console.log(response);                                            
        }).catch(function (response) {
          // handle error
           console.log(response);
        });    

        setTimeout(() => {            
            this._child.current.onLoadData();
        }, 200);

    }

    onUpdateNotes = (event) => {        
        this.setState({notes: event.target.value});
    }

    handleStatusChange = (event) => {        
        this.setState({statusOption: event.target.value});
    }

    renderHistoryTable = (event) => {        setTimeout(() => {
            return (            
                <div>
                    <AdminOfferStatusHistoryDetailSection id={this.state.formData.Id}/>
                </div>
            )                
        }, 500);
    }

    handleCheckBoxChange = (event) => {
        this.setState({ IsAnonymous: event.target.value});        
    }
    
    handleChange = (prop) => (event) => {
        this.setState({ [prop]: event.target.value});        
    }
 
    handleDateTimeChange = (event) =>  {
        this.setState({[event.target.name]: event.target.value});       
    }

    handleRadioChange = (event) => {
        this.setState({IsAnonymous: event.target.value});        
    }

    onHandleImageAdded = (event) => {
        const offerId = this.props.match.params.id;                
        this.setState({ isLoading: true });          
        const url = `/api/admin-offers/getsingleserviceoffer/${offerId}`;                        
        
        axiosConn.get(url)
        .then(results => {        
            
            
            var imgData = results.data.data.record[0].ImageList;
            
            this.setState({ imagesList: imgData});
            this.setState({ isLoading: false });          
    
        })
        .catch(function (response) {            
          console.log(response);
        });
    }

    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
        const returnLocation = this.props.match.params.rtnloc;        
        const returnTabId = this.props.match.params.tabid;
        const returnURL = '/' + returnLocation + '/' + returnTabId;
        const offerId = this.props.match.params.id;
        // console.log('this.props.match ', this.props.match);
        // console.log('this.props.match.params ', this.props.match.params);
        // console.log('returnTabId ', returnTabId);
        // console.log('returnURL ', returnURL);
        const customRenderThumb = (children) =>
            children.map((item) => {
            // console.log(item);
            return <img src={item.props.children.props.src} style={{width: '50px', maxHeight: '100px'}} />;
        });
        return (
            <div style={{ maxWidth: "100%", marginTop: "-85px", marginRight: "25px"}}>            
                <form  noValidate autoComplete="off">

                <Paper component="div" variant="outlined" style={{ padding: "45px"}}>
                    <Grid container spacing={4} >
                    
                        <Grid sm={2}  align="left">                            
                        
                        <IconButton color="primary" size="" component={Link} to={returnURL}>                        

                        <ArrowBackIcon />
                          <Typography variant="h6">
                            Back  
                          </Typography>                       
                        </IconButton>
                        
                        </Grid>

                        <Grid sm={8}  align="center">                            
                            <Typography variant="h4" gutterBottom>
                                Admin Offer Form
                            </Typography>
                        </Grid>

                        <Grid sm={12}  align="center" spacing={5}>                            
                            <Typography variant="h4" gutterBottom>
                                <Divider />
                            </Typography>
                        </Grid>

                        <Grid sm={10} style={{marginTop: '5px'}} align="center"></Grid>

                        <Grid sm={1} style={{marginTop: '5px'}} align="center"></Grid>
                        <Grid sm={6} style={{marginTop: '15px'}} align="center">
                            <Grid container spacing={4} >
                                <Grid sm={11}  style={{marginTop: '15px'}} >                                                                
                                    <ImageGalleryQuiltEditable onHandleImageAdded={this.onHandleImageAdded} offerId={offerId} imageArr={this.state.imagesData} />
                                </Grid>
                            </Grid>                                                 
                        </Grid>
                        
                        <Grid sm={5} style={{marginTop: '5px'}}>                            
                        
                            <Grid  spacing={3} style={{marginLeft: '15px'}}>
                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="h5" gutterBottom align="left"> 
                                    <b>Title:</b> {this.state.formData.Service}
                                    </Typography>                        
                                </Grid>                                                        

                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left">
                                    <b>Description:</b>  {this.state.formData.OfferDetails}
                                    </Typography>                        
                                </Grid>                                                        


                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left"> 
                                    <b>Status:</b> {this.state.statusText}
                                    </Typography>                        
                                </Grid>                                                        

                                
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Grid  container>
                                        <Grid sm={3} item align="left" >                                           
                                            <FormControl variant="outlined" size={'large'}>
                                                <InputLabel id="status">Status</InputLabel>
                                                <Select
                                                labelId="status"
                                                id="status"
                                                
                                                value={this.state.statusOption}
                                                onChange={this.handleStatusChange}
                                                label="Age"
                                                >
                                                <MenuItem value="">
                                                    <em>None</em>
                                                </MenuItem>                                                                                        
                                                <MenuItem value={0}>New</MenuItem>
                                                <MenuItem value={1}>Approved</MenuItem>
                                                <MenuItem value={99}>Rejected</MenuItem>
                                                <MenuItem value={5}>Pending</MenuItem>
                                                <MenuItem value={7}>Connected</MenuItem>
                                                <MenuItem value={9}>Closed</MenuItem>
                                                <MenuItem value={10}>Expired</MenuItem>
                                                
                                                </Select>
                                            </FormControl>
                                        </Grid>
                                        <Grid sm={5} item  align="center" >                                           
                                            <TextField id="notes" 
                                                       name="notes"
                                                       rows={4} 
                                                       fullWidth
                                                       multiline
                                                       value={this.state.notes}
                                                       onChange={this.onUpdateNotes}
                                                       size={'small'} 
                                                       label="Notes" 
                                                       variant="outlined" />
                                        </Grid>

                                        <Grid sm={2} item  align="center" >                                           
                                            <Fab color="primary" aria-label="add" onClick={this.onUpdateStatus}>
                                                <AddIcon />
                                            </Fab>         
                                        </Grid>
                                    </Grid>
                                </Grid>

                                <Grid sm={12} align="center" item style={{marginTop: '25px'}}>                                           
                                    <AdminOfferStatusHistoryDetailSection id={this.props.match.params.id} ref={this._child} />                                    
                                </Grid>

                            </Grid>   
                        
                        </Grid>
    
                    </Grid>
                </Paper>
                </form>
            </div>
        );
    }
}





