import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import ActiveRequests from '../../MaterialTable/ActiveRequests';
import ActiveOffers from '../../MaterialTable/ActiveOffers';
import ReactPlayer from 'react-player'
import HelpIcon from '@material-ui/icons/HelpOutline';
import PanToolIcon from '@material-ui/icons/PanTool';

import './Landing.css';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(5),
  },
}));

function a11yProps(index) {
  return {
    id: `scrollable-force-tab-${index}`,
    'aria-controls': `scrollable-force-tabpanel-${index}`,
  };
}


function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

export default function Landing(props) {
  const [spacing, setSpacing] = React.useState(2);
  const classes = useStyles();
  const { children, value, index, ...other } = props;  
  const [tabValue, setValue] = React.useState(0);

  const handleChange = (event) => {
    setSpacing(Number(event.target.value));
  };

  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  };
//   const services = "/images/agreement.svg";
//   const request = "/images/actions.svg";
  const offer = "/icons/four_hands.svg";
  const request = "/icons/request.svg";
  const requestPath = 'request-support';
  const offerPath = 'offer-support';

  var rootURL = '';
  // if (process.env.NODE_ENV === 'development') {      
  //   rootURL = process.env.REACT_APP_BASE_URL_LOCAL;
  // } else {
  //   if(window.location.hostname === process.env.REACT_APP_PROD_DOMAIN) {
  //     rootURL = process.env.REACT_APP_BASE_URL_PROD;
  //   } else {
  //     rootURL = process.env.REACT_APP_BASE_URL_TEST;
  //   }
  // }

  return (
    <Grid container className={classes.root} spacing={5}>

      {/* <Grid item xs={7}>
        <Grid container justify="center" spacing={spacing}>
            <Grid key={1} item>
               <MediaCard src={services} title="Receive Support" description="You can help support fellow employees by donating goods or service" path="/support"/>
            </Grid>
            <Grid key={2} item>
               <MediaCard src={request} title="Give Support" description="You can help support fellow employees by donating goods or service" path="/support"/>
            </Grid>
        </Grid>
      </Grid>
 */}

      <Grid item xs={5}>
        <Grid container justify="center" spacing={spacing}>
            <ReactPlayer  playing controls muted
            className='react-player'
            url={ rootURL + '/videos/instructions_demo.mp4'} />
        </Grid>
        {/* <Grid item xs={12}>
        <Grid container justify="center" spacing={spacing}>
            <Grid xs={3} item component={Link} to={requestPath}>
                <Paper color="text.primary" elevation={3} style={{padding: '7px', minWidth: '200px'}}  >
                    <img src={process.env.PUBLIC_URL + request} width="100"/>
                    <Typography variant="h5" component="h5">
                        Request Service
                    </Typography>
                </Paper>
            </Grid>
            <Grid xs={3} item component={Link} to={offerPath}>
                <Paper color="text.primary" elevation={3} style={{padding: '7px', minWidth: '200px'}}  >
                    <img src={process.env.PUBLIC_URL + offer} width="100"/>
                    <Typography variant="h5" component="h5">
                        Offer Service
                    </Typography>
                </Paper>               
            </Grid>
        </Grid>
      </Grid> */}

      </Grid>

      <Grid item xs={6}>
        <Grid container justify="center" spacing={spacing}>
          <Grid key={1} item>
            <Tabs
                value={tabValue}
                onChange={handleTabChange}
                indicatorColor="primary"
                textColor="primary"
                centered
                >
                <Tab label="Offers" icon={<HelpIcon />} />                                  
                <Tab label="Requests" icon={<PanToolIcon />} />
                                
              </Tabs>
              {tabValue === 0 && <TabContainer><ActiveOffers /></TabContainer>}
              {tabValue === 1 && <TabContainer><ActiveRequests /></TabContainer>}
                        
              
          </Grid>

        </Grid>
      </Grid>




    </Grid>
  );
}
