import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import OfferSupportForm from './OfferSupportForm';
// import MaterialTable from './../MaterialTable/MatDataTable';
import ActiveRequestsTable from './ActiveRequestsTable';
import Typography from '@material-ui/core/Typography';

const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default class OfferSupportLayout extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
        isRequestSelected: false,
        requestSelectedId: 0,        
        selectedRequestData: [],        
    };   
  }
  
  handleRequestSelected = (rowData, toggleVal) => (event) => {    
    console.log('Id ', rowData.Id);    
    console.log('Row Data ', rowData);    
    console.log('toggleVal ', toggleVal);    
        
    this.setState({selectedRequestData: rowData, requestSelectedId: rowData.Id, isRequestSelected: toggleVal});
  }

  removeSelectedRequest = (id) => (event) => {
    console.log('removeSelectedRequest ', id);    
    this.setState({selectedRequestData: [], 
                   requestSelectedId: 0, 
                   isRequestSelected: false});
  }

  FormRow() {
    const layout = {
      marginTop: '-75px'
    }
  

    return (
      <React.Fragment>          
        <Grid container spacing={3} style={layout}>
          <Grid item xs={5}>
            <Paper className={classes.paper}>
                <OfferSupportForm selectedRequestData={this.state.selectedRequestData} 
                                  removeSelectedRequest={this.removeSelectedRequest}
                                  isRequestSelected={this.state.isRequestSelected} 
                                  requestSelectedId={this.state.requestSelectedId}/>
            </Paper>
          </Grid>
          <Grid item xs={7}>
            <Paper className={classes.paper}>
                <ActiveRequestsTable handleRequestSelected={this.handleRequestSelected} requestSelectedId={this.state.requestSelectedId}/>
            </Paper>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  render() {
      return (
          <div>
              <Grid container spacing={1}>
                  <Grid container item xs={12} spacing={3}>
                      {this.FormRow()}
                  </Grid>
                  <Grid container item xs={12} spacing={3}>
                      
                  </Grid>
              </Grid>
                  
          </div>
      )
  }
}
