import React, {useState} from 'react';
import DatePicker from 'react-date-picker';
import { addDays } from "date-fns";
import './InputReactDatePicker.css';
export default function InputReactDatePicker(props) {
    const [startDate, setStartDate] = useState(new Date());
    return (
        <div>
           <label>{props.label} </label>
           <br/>
              <DatePicker
              className="datepicker"
                selected={props.value}
                name={props.fieldName}
                clearIcon={null}
                value={props.value}
                label={'From Date'}
                onChange={(date) => props.handleDateChange(date)}                
                // excludeDates={[new Date(), subDays(new Date(), 4)]}
                minDate={addDays(props.value, props.minDays)}
                maxDate={addDays(props.value, props.maxDays)}
                placeholderText="Select a date other than today or yesterday"
                renderCustomHeader={({
                    monthDate,
                    customHeaderCount,
                    decreaseMonth,
                    increaseMonth,
                  }) => (
                    <div>
                      <button
                        aria-label="Previous Month"
                        className={
                          "react-datepicker__navigation react-datepicker__navigation--previous"
                        }
                        style={customHeaderCount === 1 ? { visibility: "hidden" } : null}
                        onClick={decreaseMonth}
                      >
                        <span
                          className={
                            "react-datepicker__navigation-icon react-datepicker__navigation-icon--previous"
                          }
                        >
                          {"<"}
                        </span>
                      </button>
                      <span className="react-datepicker__current-month">
                        {monthDate.toLocaleString("en-US", {
                          month: "long",
                          year: "numeric",
                        })}
                      </span>
                      <button
                        aria-label="Next Month"
                        className={
                          "react-datepicker__navigation react-datepicker__navigation--next"
                        }
                        style={customHeaderCount === 0 ? { visibility: "hidden" } : null}
                        onClick={increaseMonth}
                      >
                        <span
                          className={
                            "react-datepicker__navigation-icon react-datepicker__navigation-icon--next"
                          }
                        >
                          {">"}
                        </span>
                      </button>
                    </div>
                  )}
                />
        </div>
    )
}
