import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
  formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
   },
  checked: {},    
}));
export default function InputTextAreaOffer(props) {
    const classes = useStyles();
    const CHARACTER_LIMIT = 200;
  
    return (
        <React.Fragment>
           <>                       
           <TextField
                id={props.label}
                label={props.label}
                multiline
                rows={4}
                defaultValue=""
                helperText="(Required)"
                variant="outlined"
                inputProps={{
                  maxlength: CHARACTER_LIMIT
                }}
                value={props.value}
                helperText={`${props.value.length}/${CHARACTER_LIMIT}`}
                onChange={props.handleChange("name")}
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                
                style={{ width: '78%', marginTop: 8  }}
              />
            </>            
        </React.Fragment>
    )
}
