import Skeleton, { SkeletonTheme } from "react-loading-skeleton";
import { Alert } from 'react-bootstrap';
import MediaQuery from 'react-responsive'
import { Typography } from '@material-ui/core';
import ImageCard from './ImageCard/ImageCard';
import VideoCard from './VideoCard/VideoCard';
import './VideosLayout.css';


export default class VideosLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videoArr: [],
      votingHistory: [],
      sliderValue: 10,
      chosenContestant: null,
      queryStr: '',
      loading: true,
      showAlert: true,
      votingDict: [],
      text: 'LAS PET BOWL',
      elementOutline: [1,2,3,4,5,6,7,8]
    };
    this.ref = React.createRef([]);   
    this.videoChildRef = React.createRef([]);

    // this.setState({ loading: true });          
            
  }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    // const id = this.props.eventId.match.params.eventId;                            
    // const parentURL = `/api/events/getcontestants/${id}`;                

    this.setState({ loading: true });      
    var dataArr = new Array();    
    dataArr.push({Id: 1, btnColor: 'info', Title: 'Video 1', MediaType: 1, VideoURL: 'HmZKgaHa3Fg'});        
    dataArr.push({Id: 2, btnColor: 'info', Title: 'Video 2', MediaType: 1, VideoURL: 'HmZKgaHa3Fg'});        
    dataArr.push({Id: 3, btnColor: 'info', Title: 'Image 1', MediaType: 2, VideoURL: 'HmZKgaHa3Fg'});        
    dataArr.push({Id: 4, btnColor: 'info', Title: 'Image 2', MediaType: 2, VideoURL: 'HmZKgaHa3Fg'});        
    this.setState({ videoArr: dataArr});      
    // const UserId = sessionStorage.getItem('userId'); 

  }

  componentDidUpdate() {
    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);

  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

  triggerClassChildAlert = () => {
    // console.log('Class Trigger Alert');
    this.VideoCard.showAlert();        
  }

  changeVote = (contestantId) => {
    var dataArr = [];
    // const eventId = this.props.eventId.match.params.eventId;                        
    // const UserId = sessionStorage.getItem('userId');
    // const TEXTS = [
    //   "LAS",
    //   "Pet",
    //   "Bowl"      
    // ];
    // const userVotingRecordId = sessionStorage.getItem('UserVotingRecordId');
    // const url = `/api/events/recordvote`;                
    
    // var bodyFormData = {
    //   Id: Number(userVotingRecordId),
    //   UserId: UserId,
    //   EventId: Number(eventId),
    //   ChosenContestantId: Number(contestantId)
    // }            

    // setTimeout(() => {
    //   this.setState({ votingArr: dataArr });                
      
    // }, 1000)
    
  }

  
  renderSkeletonSection = () => {
    return (            
      <Grid             
        container item
        direction="row"
        justify="left"
        alignItems="center"
        xs={12} md={12} lg={12} spacing={4}>
        {this.state.elementOutline.map((element, i) => {
            return (
            <Grid item xs={12} md={6} lg={3}>
              <SkeletonTheme color="#E0E0E0" highlightColor="#A0A0A0">
              <div className="row">
                <div className="col-4 text-left">
                <Skeleton circle={true} height={50} width={50} />
                </div>
                <div className="col-8 text-left">
                <Skeleton count={1} height={25} className="mt-3"/>
                </div>
              </div>
              
                <p>
                
                <Skeleton count={2} height={25}/>
                <Skeleton count={1} height={200}/>
                <Skeleton count={2} height={25}/>

                </p>
              </SkeletonTheme>;
            </Grid>
            )
          })}
      </Grid>           
    )
  }

  renderVideosSection = (mobile) => {
    
    return (    
      
      <Grid           
        container item        
        direction="row"
        justify={mobile ? 'center' : 'left'}
        alignItems={mobile ? 'center' : 'left'}
        xs={12} md={12} lg={12} spacing={4}>
        {this.state.videoArr.map((element, i) => {
          
          if(element.MediaType === 1) {
            return (
            <Grid item xs={12} md={6} lg={3}>
              <VideoCard  data={element}                               
                          onChangeVote={this.changeVote} 
                          title={element.Title}                                                         
                          contestantId={element.Id} 
                          url={element.VideoURL}/>
            </Grid>
            )
          }
          return (  
            <Grid item xs={12} md={6} lg={3}>                
              <ImageCard  data={element} 
                          onChangeVote={this.changeVote} 
                          title={element.Title}                           
                          contestantId={element.Id} 
                          voteId={this.state.chosenContestant}/>
            </Grid>
          )
        })}
      </Grid>  
    )
  }
 
  AlertDismissible = () => {      
    if (this.state.showAlert === true) {
      return (
        <Alert variant="info" onClose={() => this.setState({showAlert: false})} dismissible>  
          <p className="alert-msg">
              Please review each video below and vote for your favorite. Note, you can only vote once. Voting closes on 2/9.
          </p>
        </Alert>
      );
    }    
  }


  render() {            
    
    const data = this.state.contestants;

    return (
      <React.Fragment>
        {/* <Typography variant="h3" gutterBottom>
        Video Section
        </Typography>        */}
      <h1 className="title">Video Section </h1>      
      
      <div className="media-wrapper" >          
         {this.renderVideosSection(false)}
      </div>        
               
      </React.Fragment>
    );
  }
}



