import React, { Component } from 'react';
import axiosConn from '../Global/axiosConn';
import MainNav from './MainNav';


export default class MainNavParent extends Component {
  
  constructor(props) {
    super(props);
    const sList = ['Apple', 'Aptitude',  'Apply', 'Bat', 'Cat', 'Delta', 'Echo'];    
    this.state = {
        searchList: [],
        searchFilteredList: [],
        open: true,
        showResults: false,        
        searchInput: '',
        numOfItemsInCart: 0
    };           

  }


//***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {
    
    // const id = this.props.eventId.match.params.eventId;                            
    // const parentURL = `/api/events/getcontestants/${id}`;                
    const arrayIds=[21, 32];
    sessionStorage.setItem('offersShoppingList', JSON.stringify(arrayIds));
    const mobileMenuId = 'primary-search-account-menu-mobile';
    var items = JSON.parse(sessionStorage.getItem('offersShoppingList'));
        
    this.setState({ numOfItemsInCart: items.length });      
    this.setState({ loading: true });      
    var dataArr = new Array();    
    // const UserId = sessionStorage.getItem('userId'); 
  

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    const url = `/api/pub/getserviceoffers`;                
    
    axiosConn.get(url)
    .then(results => {
      this.setState({ 
        searchList: results.data.data,
        searchListOriginal: results.data.data
      });          
      
      this.setState({ isLoading: false });          
    })
    .catch(function (response) {            
      console.log(response);
    });


    
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************


  handleRouteChange = (event) => {
    // console.log('currentLocation: ', event);
  }
  handleSearchInput = (event) => {    
    
    var searchStr = event.target.value;
    var newList = [];
    var counter = 0;
    this.state.searchList.map((item, i) => {
      // console.log('item ', item);    
      
      // if(item.toLowerCase().indexOf(event.target.value)) {  // Contains
      // (item.title.toLowerCase().indexOf(searchStr) > 0 || item.title.toLowerCase().startsWith(searchStr)) && count < 3) {  
      //   newUsersList.push(item);
      if((item.Service.toLowerCase().indexOf(searchStr) > 0 || item.Service.toLowerCase().startsWith(searchStr)) && counter < 5) {
          newList.push(item);
          counter = counter + 1;
      }                    

    });    
    
    if(searchStr.length > 0) {
      this.setState({showResults: true});
    }
    else {
      this.setState({showResults: false});
    }
    
    this.setState({searchFilteredList: newList});
    this.setState({searchInput: event.target.value});

  }  

  handleCloseSearchFilter = () => {
    this.setState({showResults: false})    
  }

  handleSearchFilter = () => {
    this.setState({showResults: true})
  }
  
  handleCloseSearchFilter = () => {
    this.setState({showResults: false})    
  }
  
  handleClearSearchField = (event) => {
    console.log('event ', event);
    this.setState({searchInput: ''});
    this.setState({showResults: false});
  }
  
  handleCloseFilter = (event) => {        
    this.setState({showResults: false});
    this.setState({searchInput: ''});
  }

  render() {
    return (
      <React.Fragment>
        <MainNav searchList={this.state.searchFilteredList} 
                 handleRouteChange={this.handleRouteChange}
                 handleSearchInput={this.handleSearchInput}  
                 handleClearSearchField={this.handleClearSearchField}
                 handleCloseSearchFilter={this.handleCloseSearchFilter}
                 handleCloseFilter={this.handleCloseFilter}
                 numOfItemsInCart={this.state.numOfItemsInCart}
                 searchInput={this.state.searchInput}       
                 showResults={this.state.showResults}/>  
      </React.Fragment>
    );
  }
}