import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import axiosConn from '../../../Global/axiosConn';
function check(data, val) {
  // console.log(data);
  // console.log(val);
  var isConnected = false;
  data.forEach(item => {
      if(item === val) {
          isConnected = true;
      }
  })
  return isConnected;
};


export default function DialogConfirmConnect(props) {
  const [open, setOpen] = React.useState(false);
  // console.log('DialogConfirmConnect props ', props);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  
  const handleConnect = () => {
    const url = `/api/admin-connect/addconnect`;                    
    const userID = Number(sessionStorage.getItem('UserId'))
    var self=this;
    axiosConn.post(url, {      
        UserId: userID,
        OfferId: props.offerId,
        RequestId: props.requestId,
    })
    .then(function (response) {
        console.log(response);                                
        //  self.onLoadData();
    }).catch(function (response) {
      //handle error
       console.log(response);
    });     

    setOpen(false);
  };
  //const connData = props.connectionsData;
  const connData = [42, 38];
  // console.log('props ', props);
  return (
    <div>
       {check(connData, props.offerId) ?
        <Button variant="outlined" color="secondary" onClick={handleClickOpen}>
            Disconnect
        </Button>
        :
        <Button variant="outlined" color="primary" onClick={handleClickOpen}>
            Connect
        </Button>
       } 

      <Dialog      
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Connection Confirmation"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Connection Confirmation for  {props.offerTitle} to  {props.requestTitle}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancel
          </Button>
          <Button onClick={handleConnect} color="primary" autoFocus>
            Connect
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}