
import react, { useState } from 'react';
import ImageList from '@material-ui/core/ImageList';
import ImageListItem from '@material-ui/core/ImageListItem';
import ImageListItemBar from '@material-ui/core/ImageListItemBar';    
import IconButton from '@material-ui/core/IconButton';    
import DeleteIcon from '@material-ui/icons/Delete';
import Tooltip from '@material-ui/core/Tooltip';
import Dropzone from './Dropzone';
import axiosConn from '../../../Global/axiosConn';
import Swal from 'sweetalert2'

// import axiosConn from '@/Global/axiosConn';

export default function ImageGalleryQuiltEditable(props) {
  const maxImagesAllowed = 4;
  const [showAlert, setShowAlert]= useState(false);

  const onHideAlert = event => {
    setShowAlert(false);
  }

  const deleteImage = (id) => {
    console.log('delete image ', id);
        
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: 'btn btn-success',
        cancelButton: 'btn btn-danger'
      },
      buttonsStyling: false
    })
    
    swalWithBootstrapButtons.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      reverseButtons: true
    }).then((result) => {
      console.log('Sweet Alert result ', result)
      if (result.value === true) {        
        const url = `api/admin-offer-images/archiveimage/${id}`;
        axiosConn.get(url)
        .then(function (response) {
          console.log(response);            
          props.onHandleImageAdded();
        }).catch(function (response) {
          console.log(response);
        });  

        swalWithBootstrapButtons.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      } else if (
        /* Read more about handling dismissals below */
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
        )
      }
    })

  }
  
  console.log('ImageGalleryQuiltEditable ', props.imageArr);
  var baseURL = process.env.REACT_APP_BASE_URL_PROD;
  if (process.env.NODE_ENV === 'development') {
    baseURL = process.env.REACT_APP_BASE_URL_LOCAL;      
  } 
  return (
    <>
    <ImageList sx={{ width: 600, height: 450 }}>
      <ImageListItem key="Subheader" cols={1}>
        
        <div style={{marginLeft: '-40px', marginRight: '20px'}}>
         <Dropzone onHandleImageAdded={props.onHandleImageAdded} offerId={props.offerId} maxImagesAllowed={maxImagesAllowed}/>
        </div>
      </ImageListItem>
      {props.imageArr.map((item, i) => (
        
        item.IsArchived ? ''
        :
        <ImageListItem key={i} cols={1} rows={2}>
          <img
            
            srcSet={ item.original }
            loading="lazy"
          />
          <ImageListItemBar 
            title={item.DateCreated}
            subtitle={item.author}
            actionIcon={
              <Tooltip title="Remove Image" aria-label="Remove Image">
                <IconButton
                  type="button"
                  style={{ color: 'red' }}
                  aria-label={`info about ${item.title}`}
                  onClick={(e) => deleteImage(item.Id)}
                >
                  <DeleteIcon fontSize={'large'}/> 
                </IconButton>
              </Tooltip>
            }
          />
        </ImageListItem>        
      ))}
    </ImageList>
    </>
  );
}

