import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import FavorIcon from '@material-ui/icons/Loyalty';
import GoodsIcon from '@material-ui/icons/CardGiftcard';
import ServiceIcon from '@material-ui/icons/PanTool';

const useStyles = makeStyles((theme) => ({
    root: (props) => {
      return {
        color: theme.palette[props.color].main,
        // some other custom styles
      };
    },
    chosen:  {
        color: 'white',
        background: 'darkblue'
    }
}));

const HtmlTooltip = withStyles((theme) => ({
    tooltip: {      
      backgroundColor: '#f5f5f9',
      color: 'rgba(0, 0, 0, 0.87)',
      maxWidth: 220,
      fontSize: theme.typography.pxToRem(12),
      border: '1px solid #dadde9',
    },
  }))(Tooltip);
  
  
  
export default function CategoriesToggleButtons(props) {
  const [category, setCategory] = React.useState(0);  
  
  const [cats, setCats] = React.useState(() => [1, 2, 3]);
//   const classes = useStyles();
  const handleCategory = (event, newCategory) => {
      console.log(event)
      console.log(newCategory)
    setCats(newCategory);
  };

  const onChangeCategory = event => {
      
      props.onSelectCategory(event);
      setCategory(event);
  }

  return (
    <>      
    <ToggleButtonGroup value={cats} exclusive aria-label="Service Category">
        <HtmlTooltip
            placement="top"
            title={
            <React.Fragment>
                <Typography color="inherit">Service Category</Typography>
                {"Helping by ?"}
                <b>{'Example: '}</b> <em>{"Painting, moving, repairs, etc"}</em>
                
            </React.Fragment>
            }>

            <Button variant="contained" color={category === 1? 'primary' : ''} aria-label="Service/Assistance" onClick={() => onChangeCategory(1)}>
                Service/Assistance
            </Button>
        </HtmlTooltip>

        <HtmlTooltip
            placement="top"
            title={
            <React.Fragment>
                <Typography color="inherit">Goods/Items/Products</Typography>
                {"New or used goods?"}
                <b>{'Example: '}</b> <em>{"Furniture, clothes, electronics, etc"}</em>
                
            </React.Fragment>
            }>

            <Button value="2" variant="contained" color={category === 2? 'primary' : ''} aria-label="Goods/Items/Products" onClick={() => onChangeCategory(2)}>
                Goods/Items/Products
            </Button>
        </HtmlTooltip>


        <HtmlTooltip
            placement="top"
            title={
            <React.Fragment>
                <Typography color="inherit">Favor/Kind Gesture</Typography>
                {"?????"}
                <b>{'Example: '}</b> <em>{"???, etc"}</em>
            </React.Fragment>
            }>

            <Button value="3" variant="contained" color={category === 3? 'primary' : ''}  aria-label="Favor/Kind Gesture" onClick={() => onChangeCategory(3)}>
                Favor/Kind Gesture
            </Button>
        </HtmlTooltip>


    </ToggleButtonGroup>
    </>
  );
}
