import React from 'react';
import { green } from '@material-ui/core/colors';
import Radio from '@material-ui/core/Radio';
import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormLabel from '@material-ui/core/FormLabel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
          color: green[600],
        },
    },
    checked: {},
 }));

export default function InputRadioButtons(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(props.IsAnonymous);
    const [error, setError] = React.useState(false);
    const [selectedValue, setSelectedValue] = React.useState('No');
    const [helperText, setHelperText] = React.useState('Choose wisely');
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };

    const handleChange = (event) => {
       console.log('Radio ', event);
       // setOpen(true);         
    }

    const handleRadioChange = (event) => {
        setValue(event.target.value);
        setHelperText(' ');
        setError(false);

    };
    
    return (
        <React.Fragment>
            <FormControl component="fieldset" style={{ display: "flex",  alignItems: "left", marginLeft: "40px", marginBottom: "20px"}}>
                <FormLabel component="legend" style={{ textAlign: "left", marginLeft: "16px"}}>{props.label}</FormLabel>
                <RadioGroup row aria-label="position" value={props.value} name={props.fieldName} onChange={props.handleRadioChange}>
                    <FormControlLabel
                        value="Yes"
                        control={<Radio color="primary" />}
                        label="Yes"
                        labelPlacement="start"                        
                    />
                    <FormControlLabel
                        value="No"
                        control={<Radio color="primary" />}
                        label="No"
                        labelPlacement="start"
                    />                    
                </RadioGroup>
            </FormControl>       

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{"Use Google's location service?"}</DialogTitle>
                <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {props.body}
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                <Button onClick={handleClose} color="primary" autoFocus>
                    OK
                </Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}
