import React from 'react';
import axiosConn from '../../../Global/axiosConn';
import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { Typography } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Link, Switch } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './RequestSupportDetailsForm.css';
import { getRootURL, getStatusHelper } from '../../../Helpers/GlobalFunctions';


const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
     },
     typography: {
        body1: {
          fontWeight: 600 // or 'bold'
        }
      },
    checked: {},    
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));


export default class OfferSupportDetailsForm extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            offerId: 0,
            formData: [],
            imagesData: [],
            statusText: '',
        };   

        this.handleDateTimeChange.bind(this);
    }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************
  
  componentDidMount() {

    this.setState({ loading: true });      
    var dataArr = new Array();    

    setTimeout(() => {        
      this.setState({ loading: false });          
    }, 2000);
    
    var rootURL = getRootURL();
 

    const requestId = this.props.match.params.id;        
    
    const url = `/api/pub/getsingleservicerequest/${requestId}`;                
    axiosConn.get(url)
    .then(results => {                
        var imageArr = [];
        
        const statusVal = Number(results.data.data.Status);
        var status = getStatusHelper(statusVal);

        this.setState({ formData: results.data.data, 
                        statusText: status });

        this.setState({ isLoading: false });          

    })
    .catch(function (response) {            
      console.log(response);
    });
  }

  //***********************************************************************************************************************************
  //*  C O M P O N E N T   M E T H O D S
  //***********************************************************************************************************************************

    handleCheckBoxChange = (event) => {
        this.setState({ IsAnonymous: event.target.value});        
    };
    
    handleChange = (prop) => (event) => {
        this.setState({ [prop]: event.target.value});        
    };
 
    handleDateTimeChange = (event) =>  {
        this.setState({[event.target.name]: event.target.value});       
    };

    handleRadioChange = (event) => {
        this.setState({IsAnonymous: event.target.value});        
    };

    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
        const returnLocation = this.props.match.params.rtnloc;        
        const returnURL = '/' + returnLocation;
        const customRenderThumb = (children) =>
            children.map((item) => {
            // console.log(item);
            return <img src={item.props.children.props.src} style={{width: '50px', maxHeight: '100px'}} />;
        });
        return (
            <div style={{ maxWidth: "50vw", marginTop: "-85px", marginLeft: "auto", marginRight: "auto"}}>            
                <form  noValidate autoComplete="off">
                <Paper component="div" variant="outlined" style={{ padding: "45px"}}>
                    <Grid container spacing={4} >
                    
                        <Grid sm={2}  align="left">                            
                        
                        <IconButton color="primary" size="" component={Link}  to={returnURL}>                        
                          <ArrowBackIcon />
                          <Typography variant="h6">
                            Back  
                          </Typography>                       
                        </IconButton>
                        
                        </Grid>
                        
                        <Grid sm={10} style={{marginTop: '5px'}} align="center"></Grid>

                        <Grid sm={1} style={{marginTop: '5px'}} align="center"></Grid>
                        <Grid sm={6} style={{marginTop: '15px'}} align="center">
                            <Grid container spacing={4} >
                                <Grid sm={11}  style={{marginTop: '15px'}} >                                                                
                                    {/* <ImageCarousel dataSource={this.state.imagesData} /> */}
                                    <Carousel showThumbs={true} 
                                              autoPlay={false} 
                                              infiniteLoop={false} 
                                              showIndicators={false}
                                              showArrows={true} 
                                              dynamicHeight={false} 
                                              thumbWidth={80} 
                                              thumbHeight={100} 
                                              renderThumbs={customRenderThumb}>                    
                                              
                                        {this.state.imagesData.map((element, i) => {          
                                            return (  
                                                <div key={i}>
                                                 <img src={element.original} style={{width: '24vw', maxHeight: '85vh'}}/>                                             
         
                                                                                                                                                    
                                                </div>    
                                            )
                                        })}      
                                    </Carousel>
       

                                </Grid>
                            </Grid>                                                 
                        </Grid>
                        
                        <Grid sm={3} style={{marginTop: '5px'}}>                            
                        
                            <Grid container spacing={3} style={{marginLeft: '15px'}}>
                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="h4" gutterBottom align="left"> 
                                    {this.state.formData.Service}
                                    </Typography>                        
                                </Grid>                                                        

                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left">
                                    {this.state.formData.RequestDetails}
                                    </Typography>                        
                                </Grid>                                                        


                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left"> 
                                     Status: <b>{this.state.statusText}</b>
                                    </Typography>                        
                                </Grid>                                                        

                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    {/*  <RequestOfferFormDialog offerId={this.props.match.params.id}/> */}
                                </Grid>
                                
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>

                            </Grid>                  

                        
                        </Grid>
    
                    </Grid>
                </Paper>
                </form>
            </div>
        );
    }
}
