import React from 'react';

import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import AdminConnectionStatusHistoryTable from './AdminConnectionStatusHistoryTable';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './AdminConnectionDetailsForm.css';
import axiosConn from '../../../Global/axiosConn';
import { Typography } from '@material-ui/core';
import { getRootURL } from '../../../Helpers/GlobalFunctions';
    
const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
     },
     typography: {
        body1: {
          fontWeight: 600 // or 'bold'
        }
      },
    checked: {},    
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

export default class AdminConnectionDetails extends React.Component {
    constructor(props) {
        super(props);
        this._child = React.createRef();
        this.state = {
            connectionId: 0,
            formData: [],
            imagesData: [],
            statusOption: 0,
            statusText: '',
            connectionsDataArr: [],
            connectionsDataArrHistory: [],
            isLoading: true,
            notes: ''
        };   

        this.listRef = React.createRef();
        // console.log('process.env.REACT_APP_BASE_URL_DEV: ', process.env.REACT_APP_BASE_URL_DEV);
     
    }

  //***********************************************************************************************************************************
  //*  L I F E   C Y C L E   E V E N T S 
  //***********************************************************************************************************************************

    componentDidMount() {
        // const id = this.props.eventId.match.params.eventId;                            
        // const parentURL = `/api/events/getcontestants/${id}`;                    
        this.onLoadData();  
    }

    //***********************************************************************************************************************************
    //*  C O M P O N E N T   M E T H O D S
    //***********************************************************************************************************************************


    // *********************************************************************************************************
    // * L O A D   D A T A
    // *********************************************************************************************************

    onLoadData = (event) => {

        this.setState({ loading: true });      
        var dataArr = new Array();    
        // const UserId = sessionStorage.getItem('userId'); 
            
        setTimeout(() => {        
            this.setState({ loading: false });          
        }, 2000);
        
        var rootURL = getRootURL();
        const connectionId = this.props.id;        

        const url = `/api/admin-connect/getconnectiondetails/${connectionId}`;                
        
        axiosConn.get(url)
        .then(results => {        
            console.log('Connection Details ', results.data.data);
            
            var imageArr = [];
            var status = "";
            const statusVal = Number(results.data.data.Status);
            switch (statusVal) {
                case 0:
                    status = "Pending";
                    break;
                case 1:
                    status = "Connected";            
                    break;
                case 9:
                    status = "Closed";        
                    break;
                case 19:
                    status = "Closed - Unconfirmed";        
                    break;                        
                case 99:
                    status = "Disconnect";        
                    break;
            
                default:
                    status = "N/A";        
                    break;
            }

            this.setState({ formData: results.data.data,                             
                            statusText: status, 
                            statusOption: statusVal                            
                            });

        })
        .catch(function (response) {            
            console.log(response);
        });

        const connUrl = `/api/admin-connect/getstatushistory/${connectionId}`;                    
        axiosConn.get(connUrl)
        .then(results => {
            console.log('History results ', results);

            this.setState({ 
                connectionsDataArr: results.data.data,
                connectionsDataArrHistory: results.data.data
            });          
            
            this.setState({ isLoading: false });          
        })
        .catch(function (response) {            
           console.log(response);
        });
        
        
        setTimeout(() => {
            console.log('this.state.connectionsDataArr ', this.state.connectionsDataArr);
        }, 300);
    }

    clearForm() {
        this.state.setState({notes: ''});
    }

    // *********************************************************************************************************
    // * A D D   S T A T U S   A N D   N O T E S
    // *********************************************************************************************************

    onAddStatus = (event) => {        
        const url = `/api/admin-connect/addstatusnotes`;                    
        const userID = Number(sessionStorage.getItem('UserId'))
        var self=this;
        axiosConn.post(url, {
            RecordId: this.props.id,
            UserId: userID,
            Status: this.state.statusOption,
            Notes: this.state.notes
        })
        .then(function (response) {            
            self.onLoadData();
            self.clearForm();
        }).catch(function (response) {
          //handle error
           console.log(response);
        });    
        
        const connectionId = this.props.id;        

        const connUrl = `/api/admin-connect/getstatushistory/${connectionId}`;                    
        axiosConn.get(connUrl)
        .then(results => {            
            this.setState({ 
                connectionsDataArr: results.data.data,
                connectionsDataArrHistory: results.data.data,                
                isLoading: false
            });                      
        })
        .catch(function (response) {            
           console.log(response);
        });


    }

    onUpdateNotes = (event) => {        
        this.setState({notes: event.target.value});
    }

    handleStatusChange = (event) => {        
        this.setState({statusOption: event.target.value});
    }
    
    handleChange = (prop) => (event) => {
        this.setState({ [prop]: event.target.value});        
    }
 
    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
//         const returnLocation = this.props.match.params.rtnloc;        
//         const returnTabId = this.props.match.params.tabid;
//        const returnURL = '/' + returnLocation + '/' + returnTabId;
        
        const getStatusColor = (status) => {
            var statusColor = 'darkgreen';
            switch(status) {
                case 0: statusColor = 'barkblue'; break;
                case 1: statusColor = 'darkgreen'; break;
                case 5: statusColor = 'darkgreen'; break;
                case 7: statusColor = 'darkgreen'; break;
                case 9: statusColor = 'black'; break;
                case 10: statusColor = 'black'; break;
                case 99: statusColor = 'darkred'; break;
            }

            return statusColor;
        }

        return (
            <div style={{ width: "100%"}}>            
                <form  noValidate autoComplete="off">                               

                    <Grid container spacing={4} >                    

                        <Grid sm={12}  align="center" spacing={5}>                            
                            <Typography variant="h4" gutterBottom>
                                <Divider />
                            </Typography>
                        </Grid>

                        <Grid sm={12} style={{marginTop: '5px'}} >                            
                        
                            <Grid  spacing={3} style={{marginLeft: '15px'}} >

                                <Paper style={{padding: '20px'}}>
                                    
                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="h5" gutterBottom align="left"> 
                                     Status History
                                    </Typography>                        

                                </Grid>                                                        

                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>
                                
                                <Grid sm={12} align="center" style={{marginTop: '15px'}} >                            
                                    <Typography variant="subtitle2" gutterBottom align="left">
                                    <b>Offer:</b> {this.state.formData.OfferDetails}
                                    </Typography>                        
                                </Grid>                                                        

                                <Grid sm={12} align="center" style={{marginTop: '15px'}} >                            
                                    <Typography variant="subtitle2" gutterBottom align="left">
                                    <b>Request:</b> {this.state.formData.RequestDetails}
                                    </Typography>                        
                                </Grid>                                 

                                <Grid sm={12} align="center" style={{marginTop: '15px'}}>                            
                                    <Typography variant="body2" gutterBottom align="left"> 
                                     <b>Status:</b> {this.state.statusText}
                                    </Typography>                        
                                </Grid>                                                        

                                
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Divider />
                                </Grid>
                                <Grid sm={12} align="left" style={{marginTop: '25px'}}>                            
                                    <Grid  container>
                                        <Grid sm={3} item align="left" >                                           
                                            <FormControl variant="outlined" size={'small'}>
                                                <InputLabel id="status">Status</InputLabel>
                                                <Select
                                                labelId="status"
                                                id="status"
                                                
                                                value={this.state.statusOption}
                                                onChange={this.handleStatusChange}
                                                label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>                                        
                                                    <MenuItem value={0}>Pending</MenuItem>
                                                    <MenuItem value={1}>Connected</MenuItem>
                                                    <MenuItem value={9}>Closed</MenuItem>
                                                    <MenuItem value={19}>Closed - Unconfirmed</MenuItem>
                                                    <MenuItem value={99}>Disconnect</MenuItem>

                                                </Select>
                                            </FormControl>
                                        </Grid>
                                        <Grid sm={5} item  align="center" >                                           
                                            <TextField id="notes" 
                                                       name="notes"
                                                       rows={4} 
                                                       fullWidth
                                                       multiline
                                                       value={this.state.notes}
                                                       onChange={this.onUpdateNotes}
                                                       size={'small'} 
                                                       label="Notes" 
                                                       variant="outlined" />
                                        </Grid>

                                        <Grid sm={2} item  align="center" >                                 
                                            {this.state.notes.length > 2 ?           
                                            <Fab color="primary" aria-label="add" onClick={this.onAddStatus}>
                                                <AddIcon />
                                            </Fab>         
                                            : 
                                            <Fab color="primary" aria-label="add" disabled={true}>
                                                <AddIcon />
                                            </Fab>         

                                            }
                                        </Grid>
                                    </Grid>
                                </Grid>
                                </Paper>
                                <Grid sm={12} align="center" item style={{marginTop: '25px'}}>              
                                    {this.state.isLoading ? '' :
                                    <AdminConnectionStatusHistoryTable data={this.state.connectionsDataArr} 
                                                                       id={this.props.id} 
                                                                       ref={this._child} />            
                                    }                        
                                </Grid>
                            </Grid>                           
                        </Grid>    
                    </Grid>                
                </form>
            </div>
        );
    }
}

