import React, { Component } from 'react'
import axiosConn from '../../Global/axiosConn';
import ConnectionsTable from './ConnectionsTable';


export default class ConnectionsParent extends Component {
    constructor(props) {
    super(props);
    const sList = ['Apple', 'Aptitude',  'Apply', 'Bat', 'Cat', 'Delta', 'Echo'];    
    this.state = {
        connectionData: [],
        searchFilteredList: [],
        open: true,
        showResults: false,        
        searchInput: '',
        numOfItemsInCart: 0
    };        
}

componentDidMount() {
  
    const url = `/api/admin/getconnections`;                
    
    axiosConn.get(url)
    .then(results => {
      // console.log('Connections ', results);
      this.setState({connectionData: results.data.data});     
    })
    .catch(function (response) {            
      console.log(response);
    });

   }

   render() {
        return (
            <div>
              <ConnectionsTable data={this.state.connectionData} />
            </div>
        )
    }
}
