import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import RequestSupportForm from './RequestForm/RequestSupportForm';
// import MaterialTable from './../MaterialTable/MatDataTable';
import ActiveRequests from './../MaterialTable/ActiveRequests';

import Paper from '@material-ui/core/Paper';

const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

const rootCls = {
  display: 'flex',
  flexWrap: 'wrap',
  '& > *': {
    margin: '10px',
    width: '10px',
    height: '10px',
  },
};

export default class SubmissionFormLanding extends Component {

    FormRow() {
      return (
        <React.Fragment>
          <Grid container spacing={3} >
            <Grid item xs={5}>
              <Paper className={classes.paper}>
                <RequestSupportForm />
              </Paper>
            </Grid>
            <Grid item xs={7}>
              <Paper className={classes.paper}>
                <ActiveRequests />
              </Paper>
            </Grid>
          </Grid>
        </React.Fragment>
      );
    }

    render() {
        return (
          <div className={rootCls}>
            <Grid container spacing={1}>
              <Grid container item xs={12} spacing={3}>
                <Paper elevation={0} >
                  123
                </Paper>
              </Grid>
              <Grid container item xs={12} spacing={3}>                                            
                <Paper elevation={3} >
                  123
                </Paper>
              </Grid>
            </Grid>                    
          </div>
        )
    }
}
