import * as actionTypes from './actions';

const initialState = {    
    queryStr: '',
    id: 0,
    checkbox: false,
    sliderValue: 10, 
    adminConnectData: [],
    userConnectData: [],
    successConnectData: [],
    adminConnectRecord: [],
    userConnectRecord: [],
    successfulConnectRecord: []
};


const reducer = ( state = initialState, action ) => {
    // console.log('Action Type', action.type); //  a new hashed long string from server
    // console.log('Save Search State ', state); //  a new hashed long string from server

    switch (action.type) {
      case actionTypes.USERS_SAVE_SEARCH:
        // console.log('SAVE_SEARCH') 
        return {
        ...state,          
          checkbox: true,
          // queryStr: action.queryStr,
          // sliderValue: action.sliderValue          
      };
     
      case actionTypes.SAVE_CHECKED_SEARCH:
        // console.log('SAVE_CHECKED_SEARCH');
        return Object.assign({}, state, {
          checkbox: true,
          queryStr: 'action.type',
          sliderValue: action.sliderVal
      })

      // **********************************************************************************************************************
      // * A D M I N   S A V E   U S E R   C O N N E C T   D A T A 
      // **********************************************************************************************************************

      case actionTypes.ADMIN_SAVE_USER_CONNECT_DATA:
        // console.log('ADMIN_SAVE_USER_CONNECT_DATA', action.userConnectData);
        return Object.assign({}, state, {
          userConnectionsData: action.userConnectData
        }
      )

      // **********************************************************************************************************************
      // * S A V E   A D M I N   C O N N E C T   D A T A 
      // **********************************************************************************************************************

      case actionTypes.ADMIN_SAVE_ADMIN_CONNECT_DATA:
        // console.log('ADMIN_SAVE_ADMIN_CONNECT_DATA', action.adminConnectData);
        return Object.assign({}, state, {
          adminConnectionsData: action.adminConnectData
        }
      )

      // **********************************************************************************************************************
      // * S A V E   S U C C E S S F U L   C O N N E C T   D A T A 
      // **********************************************************************************************************************
      
      case actionTypes.ADMIN_SAVE_SUCCESSFUL_CONNECT_DATA:
        // console.log('ADMIN_SAVE_SUCCESSFUL_CONNECT_DATA', action.successConnectData);
        return Object.assign({}, state, {
          successfulConnectionsData: action.successConnectData
        }
      )

      // **********************************************************************************************************************
      // * S A V E   U S E R   C O N N E C T   D A T A 
      // **********************************************************************************************************************

      case actionTypes.ADMIN_UPDATE_USER_CONNECT_RECORD:
        // console.log('ADMIN_UPDATE_USER_CONNECT_RECORD', action.userConnectRecord);
        return Object.assign({}, state, {
          adminConnectionsData: action.userConnectRecord
        }
      )
      
      // **********************************************************************************************************************
      // * M O V E   U S E R   C O N N E C T   R E C O R D   T O   A D M I N   C O N N E C T   L I S T
      // **********************************************************************************************************************

      case actionTypes.ADMIN_MOVE_USER_CONNECT_RECORD_TO_ADMIN_CONNECT_LIST:        
        // console.log('ADMIN_MOVE_USER_CONNECT_RECORD_TO_ADMIN_CONNECT_LIST action ', action.id);  

        state.userConnectionsData.forEach((item, i) => {
          console.log('ADMIN_MOVE_USER_CONNECT_RECORD_TO_ADMIN_CONNECT_LIST item ', item);  
          if(item.Id===action.id) {
            // newAdminRecord=item;
            item.Status = 1;          
            item.OfferStatus = 7;  
            item.RequestStatus = 7;
            state.adminConnectionsData.push(item);
            state.userConnectionsData.splice(i,1);
            console.log('ADMIN_MOVE_USER_CONNECT_RECORD_TO_ADMIN_CONNECT_LIST ', state.userConnectionsData);  
          }
        });        
        return Object.assign({}, state, {
          userConnectionsData: state.userConnectionsData,
          adminConnectionsData: state.adminConnectionsData
        }        
      )

      // **********************************************************************************************************************
      // * M O V E   A D M I N   C O N N E C T   R E C O R D   T O   U S E R   C O N N E C T   L I S T
      // **********************************************************************************************************************

      case actionTypes.ADMIN_MOVE_ADMIN_CONNECT_RECORD_TO_USER_CONNECT_LIST:        
      
        state.userConnectionsData.forEach((item, i) => {
          if(item.Id===action.id) {            
            item.Status = 0;            
            state.userConnectionsData.push(item);
            state.adminConnectionsData.splice(i,1);
          }
        });        
        return Object.assign({}, state, {
          userConnectionsData: state.userConnectionsData,
          adminConnectionsData: state.adminConnectionsData,          
        }        
      )

      // **********************************************************************************************************************
      // * M O V E   A D M I N   C O N N E C T   R E C O R D   T O   S U C C E S S F U L   C O N N E C T   L I S T
      // **********************************************************************************************************************

      case actionTypes.ADMIN_MOVE_ADMIN_CONNECT_RECORD_TO_SUCCESSFUL_CONNECT_LIST:        
        // console.log('ADMIN_MOVE_ADMIN_CONNECT_RECORD_TO_SUCCESSFUL_CONNECT_LIST ', action.id);
        state.adminConnectionsData.forEach((item, i) => {
          
          if(item.Id===action.id) {          
            console.log('ADMIN_MOVE_ADMIN_CONNECT_RECORD_TO_SUCCESSFUL_CONNECT_LIST ', item);
            item.Status = 9;            
            item.OfferStatus = 9;            
            item.RequestStatus = 9;            
            state.successfulConnectionsData.push(item);
            state.adminConnectionsData.splice(i,1);
          }
        });        
        return Object.assign({}, state, {
          adminConnectionsData: state.adminConnectionsData,
          successfulConnectionsData: state.successfulConnectionsData
        }        
      )
   
   
      case actionTypes.SAVE_SEARCH_STR:
        return action.payload
      default:
        return state
    }

  }
  export default reducer;


  // case actionTypes.SAVE_UNCHECKED_SEARCH:
  //   console.log(action.text) 
  //   return Object.assign({}, state, {
  //     checkbox: false,
  //     searchFilter: action.text
  // })
  // case actionTypes.GET_CHECKBOX:
  //   console.log(action.text) 
  //   return Object.assign({}, state, {
  //     checkbox: false,
  //     searchFilter: action.text
  // })