import React, { Component } from 'react'
import './SearchBarFilter.css';
import FilteredList from './FilteredList';


export default function SearchBarFilter(props) {
    return (
        <React.Fragment>
        {props.show ?
            <div className="box">
                <FilteredList searchInput={props.searchInput} list={props.list} handleCloseFilter={props.handleCloseFilter}/>
            </div>   
            :
            ''
        } 
        </React.Fragment>
    )
}
