import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import ImageGallery from 'react-image-gallery';
import './ImageContainer.css';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { getRootURL } from '../../../Helpers/GlobalFunctions';


const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(2),
  },
  image: {
      width: '30px',
      height: 'auto'
  },

}));


export default function ImageContainer(props) {
  // console.log(images);
  var rootURL = getRootURL();

  const imageCls = {
    width: '30px',
    height: 'auto'
  }
  const onRenderImages = () => {
    
    const images = props.data.Images[0];
    const isImages = props.data.Images.length == 0 ? false : true;
    
    if(isImages == true)  {
    return (
      <Carousel showThumbs={false} autoPlay={true} infiniteLoop={true} dynamicHeight={true}>                    
        {props.data.ImageList.map((element, i) => {          
          
          return (  
            <div key={i}>          
            <img src={rootURL + '/Content/offers/' + props.data.Images[i]} className={classes.image} style={{width: '12vw', maxHeight: '35vh'}}/> 
            {/* <img key={i} src={rootURL + '/api/pub/getsingleimage/' + props.data.ImageList[i].Id} className={imageCls} style={{width: '14vw', maxHeight: '35vh'}}/> */}
            {/* <p className="legend">{props.title}</p> */}
            </div>    
          )
        })}      
      </Carousel>
    )
    } else {
      
      let imageURL = process.env.PUBLIC_URL + '/images/svg/003-hammer.svg';
      let categoryText = '';
      if(props.data.CategoryId == 1) {
        imageURL = process.env.PUBLIC_URL + '/images/svg/services.svg';
        categoryText = 'Service/Assistance';
      } 
      if(props.data.CategoryId == 2) {
        imageURL = process.env.PUBLIC_URL + '/images/svg/goods.svg';
        categoryText = 'Goods/Items/Products';
      } 
      if(props.data.CategoryId == 3) {
        imageURL = process.env.PUBLIC_URL + '/images/svg/kind_gesture.svg';
        categoryText = 'Favor/Kind Gesture';
      } 

      return (                  
          <Carousel showThumbs={false} autoPlay={false}>                    
          <div>          
            <img src={imageURL} className={classes.image} style={{width: '14vw', maxHeight: '35vh'}}/>  
            <p className="legend">{categoryText}</p>
          </div>    
          </Carousel>        
      )

    }
  }               

  return (
    <div>
    {onRenderImages()}
    </div>
  )
}

