import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';

import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import OpenInBrowserIcon from '@material-ui/icons/OpenInBrowser';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { Link } from 'react-router-dom';
import { getRootURL } from '../Helpers/GlobalFunctions';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,    
    backgroundColor: theme.palette.background.paper,
  },
  large: {
    width: theme.spacing(12),
    height: theme.spacing(12),
  },
}));


export default function FilteredList(props) {
  const classes = useStyles();
  const numbers = [1, 2, 3, 4, 5];

  const listItems = numbers.map((number) =>
    <li key={number.toString()}>
        {number}
    </li>
  );


  var rootURL = getRootURL();

  return (
    <React.Fragment>
    
    <List className={classes.root}>
    {props.list.map((item) => (
                                    
        <ListItem button component={Link} to={`/offer-support-details/${item.Id}/landing`}>
         <ListItemAvatar>
            <Avatar variant="square" src={rootURL + '/Content/offers/' + item.Images[0]} className={classes.large}  >
                
            </Avatar>
            </ListItemAvatar>
               <ListItemText primary={item.Service + ' -  2 Days Old' }  onClick={props.handleCloseFilter} 
                          secondary={
                            <React.Fragment>
                              <Typography
                                component="span"
                                variant="body2"
                                className={classes.inline}
                                color="textPrimary"
                              >                                
                                {item.Service} - 
                              </Typography>
                              {item.OfferDetails}
                              
                              <Grid container spacing={3} >
                              <Grid sm={3} align="left" style={{marginLeft: '15px', marginTop: '15px'}}>                                                              
                                
                              

                                  
                              </Grid>
                              <Grid sm={8} align="right" style={{ marginTop: '15px'}}>                                                              
                                  <b>2 Days Old</b>                                  
                              </Grid>
                            </Grid>


                            </React.Fragment>
                          }
            style={{marginLeft: '20px'}}/>
         </ListItem>
         
     ))}
     </List>
    </React.Fragment>
  );
}