import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import RequestSupportForm from './RequestSupportForm';
// import MaterialTable from './../MaterialTable/MatDataTable';
import ActiveRequests from '../../MaterialTable/ActiveRequests';
import ActiveOffersTable from './ActiveOffersTables';

const classes = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  layout: {
    marginTop: '-50px'
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default class RequestSupportLayout extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
        isOfferSelected: false,
        offerSelectedId: 0,        
        selectedOfferData: [],        
    };   
  }
  
  handleOfferSelected = (rowData, toggleVal) => (event) => {    
    this.setState({selectedOfferData: rowData, 
                   offerSelectedId: rowData.Id, 
                   isOfferSelected: toggleVal});
  }

  removeSelectedOffer = (id) => (event) => {
    console.log('removeSelectedOffer ', id);    
    this.setState({selectedOfferData: [], 
                   offerSelectedId: 0, 
                   isOfferSelected: false});
  }

  FormRow() {
    const layout = {
      marginTop: '-75px'
    }
  

    return (
      <React.Fragment>
        <Grid container spacing={3} style={layout}>
            <Grid item xs={5}>
              <Paper className={classes.paper}>
                <RequestSupportForm selectedOfferData={this.state.selectedOfferData} 
                                    removeSelectedOffer={this.removeSelectedOffer}
                                    isOfferSelected={this.state.isOfferSelected} 
                                    offerSelectedId={this.state.offerSelectedId} />
              </Paper>
            </Grid>
            <Grid item xs={7}>
              <Paper className={classes.paper}>
                <ActiveOffersTable handleOfferSelected={this.handleOfferSelected} 
                                    offerSelectedId={this.state.offerSelectedId} />
              </Paper>
            </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  render() {
    return (
      <div>
        <Grid container spacing={1} >
          <Grid container item xs={12} spacing={3}>
              {this.FormRow()}
          </Grid>
          <Grid container item xs={12} spacing={3}>
              
          </Grid>
        </Grid>                  
      </div>
    )
  }
}
