import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    '& > *': {
      margin: theme.spacing(6),
      width: theme.spacing(36),
      height: theme.spacing(36),
    },
  },
  tile: {
    cursor: 'pointer',
  },

}));

export default function SupportParentLayout() {
  const classes = useStyles();
  const requestImg = '/request.svg';
  const donateImg = '/donate.svg';
  const requestURL = '/request-support';
  const offertURL = '/offer-support';
  const history = useHistory();
  
  const goRequest = (event) => {    
    history.push(requestURL);
  }
  const goDonate = (event) => {    
    history.push(offertURL);
  }
  return (
    <>
      <Grid container spacing={3}>
        <Grid item xs={3}></Grid>
        <Grid item xs={3}>
          <div className={classes.root}>                    
            <Paper elevation={3} className={classes.tile} onClick={goRequest}>
              <Typography variant="h2" color="primary" >
                Request
              </Typography>
              <img src={process.env.PUBLIC_URL + requestImg} width="200" height="auto" style={{marginTop: '40px'}}/>
            </Paper>
          </div>
        </Grid>
        <Grid item xs={3}>
          <div className={classes.root}>                    
            <Paper elevation={3} className={classes.tile} onClick={goDonate}>
              <Typography variant="h2" color="primary">
                Donate
              </Typography>
              <img src={process.env.PUBLIC_URL + donateImg} width="200" height="auto" style={{marginTop: '40px'}}/>
            </Paper>
          </div>
        </Grid>
      </Grid>
    </>
  );
}