import React, {Component} from 'react';
import axiosConn from '../../../Global/axiosConn';
import { green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import InputTextFieldWide from '../Shared/InputTextFieldWide';
import InputRadioButtons from '../Shared/InputRadioButtons';
import InputPhoneField from '../Shared/InputPhoneField';
import { Typography } from '@material-ui/core';
import InputSelect from '../Shared/InputSelect';
import InputTextAreaRequest from '../Shared/InputTextAreaRequest';
import InputEmailField from '../Shared/InputEmailField';
import Button from '@material-ui/core/Button';
// import InputCheckBox from '../Shared/InputCheckBox';
import SweetAlert from 'sweetalert2-react';
import Divider from '@material-ui/core/Divider';
import CategoriesToggleButtons from './CategoriesToggleButtons';
import { Redirect } from 'react-router-dom';
import * as EmailValidator from 'email-validator';

const classes = makeStyles((theme) => ({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
        marginLeft: '30%'
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
        
     },
     dividerFullWidth: {
        margin: `5px 0 0 ${theme.spacing(2)}px`,
      },
      dividerInset: {
        margin: `5px 0 0 ${theme.spacing(9)}px`,
      },
    checked: {},    
}));


export default class RequestSupportForm extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            jobs: [],
            jobsHistory: [],
            loading: true,
            searchStr: '',
            Name: '',
            RequestDetails: '',
            Phone: '',
            Email: '',
            isValidEmail: false,
            Service: '',
            OnBehalfOf: 'No',
            OnBehalfOfUsername: '',
            
            IsReviewed: false,
            showAlert: false,
            CategoryId: 0,
            IsAnonymous: 'No',
            isFormNotValid: true,
            redirect: false
        };   
        this.handleDateTimeChange.bind(this);
    }

    handleCheckBoxChange = (event) => {
        this.setState({ IsAnonymous: event.target.value});                
    };

    getValidForm = (event) => {
      if (this.state.isValidEmail) this.setState({isFormNotValid: false})
      else this.setState({isFormNotValid: true})      
    }

    handleChange = (prop) => (event) => {        
        this.setState({ [prop]: event.target.value});        
    };
 
    handleOBHChange = (event) => {        
        this.setState({ OnBehalfOf: event.target.value});        
    };

    handleEmailChange  = (event) => {
        const val = event.target.value;
        
        // let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      
        // var isValid = re.test(val) ? true: false;
        this.setState({ Email: val});        
        if(EmailValidator.validate(event.target.value)) {
            this.setState({ isValidEmail: true});              
        } else {
            this.setState({ isValidEmail: false});              
        }


    };
 

    handleDateTimeChange = (fieldName, date) =>  {
        // console.log('Parent fieldName ', fieldName);        
        // console.log('Parent date ', date);        
        this.setState({[fieldName]: date});       
    };

    handleAnonymousRadioChange = event => {        
        this.setState({IsAnonymous: event.target.value});        
    };

    handleDescriptionChange = name => event => {        
        this.setState({RequestDetails: event.target.value});
    };

    handleCategoryChange = (val) => {              
        console.log('handleCategoryChange ', val.target.value);
        this.setState({CategoryId: val.target.value});                        
    };

    handlePhoneChange = (event) => {
        this.setState({Phone: event.target.value});                        
    };
    
    clearForm = (event) => {        
        this.setState({
            CategoryId: 0,
            Name: '',
            RequestDetails: '',
            OnBehalfOf: '',
            IsAnonymous: 0,
            Email: '',
            Phone: '',
        });
    }

    handleSubmitForm = (event) => {                 
        const url = `/api/pub/addservicerequest`;            
        const userID = Number(sessionStorage.getItem('UserId'))                    
        var self=this;
        axiosConn.post(url, {
            UserId: userID,            
            Service: this.state.Service,
            IsAnonymous: this.state.IsAnonymous === 'Yes' ? true : false,
            IsOnBehalfOf: this.state.OnBehalfOf === 'Yes' ? true : false,
            OnBehalfOf: this.state.OnBehalfOfUsername,            
            Name: this.state.Name,
            ChosenOfferId: this.props.offerSelectedId,
            RequestDetails: this.state.RequestDetails,
            Phone: this.state.Phone,
            Email: this.state.Email
        }).then(function (response) {
           // console.log(response);
            if(response.status === 200){
                
                // self.clearForm(response);
                console.log(response.status);
            }
            self.setState({showAlert: true})        

        }).catch(function (response) {
            //handle error
            console.log(response);
        });    
    }

    hideAlert = (event) => {
        this.setState({showAlert: false, redirect: true});        
    }

    render() {
        const categoryArr = ['Service/Assistance', 'Goods/Items/Products', 'Favor/Kind Gesture'];
        if (this.state.redirect) {
            return <Redirect to='/landing'/>;
        }

        return (
            <div style={{  marginLeft: "10px", marginTop: "10px" }}>            
                <form noValidate autoComplete="off">
                    <Grid container spacing={3} >
                        
                        <Grid sm={12} style={{marginTop: '25px'}}>                            
                            <Typography variant="h4" gutterBottom>
                                Request
                            </Typography>                        
                        </Grid>
                                                
                        <Grid sm={12} align="center">
                            {this.props.isOfferSelected ? 
                            <>
                                <Grid sm={12} >      
                                    <Divider  />
                                </Grid>
                                <Grid container >                        

                                    <Grid sm={6} style={{marginTop: '25px'}}>      
                                        <Typography variant="h6" color="primary">
                                            Connect to Offer
                                        </Typography>
                                        <Typography variant="subtitle2" color="primary">                                    
                                            {this.props.selectedOfferData.OfferDetails}   
                                        </Typography>
                                    </Grid>
                                    <Grid sm={3} style={{marginTop: '25px'}}>      
                                        <Button size="small" align="left" variant="outlined" color="secondary" onClick={this.props.removeSelectedOffer(this.props.offerSelectedId)}>Remove Connection</Button>                                
                                    </Grid>

                                 </Grid>
                                 <Grid sm={12} style={{marginTop: '20px'}}>      
                                        <Divider  />
                                 </Grid>
                                </>
                            : ''}
                        </Grid>
{/* 
                        <Grid sm={12} style={{marginTop: '20px'}}>
                            <CategoriesToggleButtons onSelectCategory={this.handleCategoryChange}/>
                        </Grid> */}

                    
                        <Grid sm={5} style={{ marginLeft: '45px', marginTop: '25px'}}>
                            <InputTextFieldWide placeholder="" helperText="(Required)" label="Name" fieldName="Name" handleChange={this.handleChange} value={this.state.Name}/>
                        </Grid>
                        <Grid sm={5} align="left" >
                        </Grid>

                        <Grid sm={1} style={{marginLeft: '5px', marginTop: '25px'}}></Grid>

                        <Grid sm={4} align="left" style={{marginLeft: '25px', marginTop: '25px'}}>                    
                            <InputRadioButtons label="On behalf of another?" value={this.state.OnBehalfOf} fieldName="OnBehalfOf" handleRadioChange={this.handleOBHChange} />
                                {/* <InputCheckBox OnBehalfOf={this.state.OnBehalfOf} handleOBHChange={this.handleOBHChange}/> */}
                        </Grid>

                        <Grid sm={6} align="left" style={{marginLeft: '70px', marginTop: '25px'}}>                                                                          
                            {(() => {
                                switch (this.state.OnBehalfOf) {
                                case "Yes": return (
                                <>  
                                    <Typography variant="body1" display="block" gutterBottom style={{marginLeft: '5px'}} align="left">
                                        Person you are submitting the request for
                                    </Typography> 

                                    <InputTextFieldWide label="On behalf of name" 
                                                        fieldName="OnBehalfOfUsername" 
                                                        helperText="(Required)"
                                                        handleChange={this.handleChange} 
                                                        value={this.state.OnBehalfOfUsername} />                         
                                </>
                                );
                                case "No": return (
                                <>                                     
                                    <div style={{marginLeft: '-47px'}}>
                                        <InputRadioButtons  label="Do you wish to remain anonymous?                        " 
                                                        value={this.state.IsAnonymous} 
                                                        fieldName="IsAnonymous"                                                         
                                                        handleRadioChange={this.handleAnonymousRadioChange} 
                                                        value={this.state.IsAnonymous}/>
                                    </div>
                                </>

                                );

                                default:   return '';
                                }
                            })()}                            
                        </Grid>         
                        
                        <Grid sm={5} style={{marginLeft: '75px', marginTop: '17px'}}>
                            <InputEmailField isValid={this.state.isValidEmail} 
                                                placeholder="" 
                                                helperText="" 
                                                label="Email" 
                                                helperText="(Required)"
                                                fieldName="Email" 
                                                isFullWidth={true}
                                                handleEmailChange={this.handleEmailChange} 
                                                value={this.state.Email}/>
                        </Grid>

                        <Grid sm={5} style={{marginTop: '10px'}}>
                            <InputPhoneField placeholder="" helperText="(Required)" label="Phone" fieldName="Phone" handleChange={this.handlePhoneChange} value={this.state.Phone}/>
                        </Grid>

                        <Grid sm={12} style={{marginTop: '20px'}}>
                            <InputTextFieldWide label="Title" helperText="(Required)" fieldName="Service" handleChange={this.handleChange} value={this.state.Service}/> 
                        </Grid>

                        <Grid sm={12}>
                            <InputTextAreaRequest label="Description"  fieldName="Description" handleChange={this.handleDescriptionChange} value={this.state.RequestDetails}/> 
                        </Grid>


                        <Grid sm={12} align="right" style={{marginRight: '65px', marginTop: '25px', marginBottom: '25px'}}>
                            
                            {(this.state.Name.length < 3 || this.state.Service.length < 3 || this.state.isValidEmail === false)?
                            <Button variant="contained"  color="primary" disabled>                             
                                Submit Request
                            </Button>
                            : 
                            <Button variant="contained"  color="primary" onClick={this.handleSubmitForm}>                             
                                Submit Request
                            </Button>

                            }
                        </Grid>

                        <Grid sm={12} align="center" style={{marginLeft: '65px', marginTop: '25px', marginBottom: '25px'}}>
                            <Typography variant="h6" display="block" gutterBottom style={{marginLeft: '5px'}}>
                                Requests will be posted for 30 days
                            </Typography>                            
                        </Grid>                         
                    </Grid>
            
                </form>

                <SweetAlert
                    success
                    show={this.state.showAlert}
                    title="Thank You!"                    
                    text="Your request has been submitted"
                    onConfirm={this.hideAlert}
                    >                    
                </SweetAlert>
            </div>
        );
    }
}
