import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Moment from 'react-moment';
import ImageContainer from './ImageContainer';
import './ImageCard.css';
import { Link } from 'react-router-dom';
import axiosConn from '../../../Global/axiosConn';

const classes = makeStyles((theme) => ({
  root: {
    
    maxWidth: '10vw',
    minHeight: '30vh',
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(2),
  },
  image: {
      width: '30px',
      height: 'auto'
  },

}));


export default function ImageCard(props) {
  const [liked, setLiked] = React.useState(false);

  const handleLikeEvent = (event) => {
    liked ? setLiked(false) : setLiked(true);
    
  };

  var cardStyle = {
    position: 'relative',
    display: 'block',
    width: '15vw',
    transitionDuration: '0.3s',
    height: '24vw'
  }

  const lookupStatus = (status) => {
    var statText = '';
    switch (status) {
      
        case 0: statText = 'New'; break;
        case 1: statText = 'Approved'; break;
        case 2: statText = 'Connected By User'; break;        
        case 5: statText = 'Processing'; break;        
        case 9: statText = 'Closed'; break;
        case 20: statText = 'Expired'; break;
        case 99: statText = 'Rejected'; break;

      default:
        break;

    }
    return statText;
  }
  
  return (      
    
    <div>        
      <Card style={cardStyle}>
        <CardHeader title={props.data.Service}         
                    titleTypographyProps={{variant:'h6' }}                    
                    subheaderTypographyProps={{variant:'body2' }}                                        
                    subheader={<Moment fromNow>{props.data.DateApproved}</Moment>}/>

          <CardMedia style={{marginTop: '1px'}}>
            <Link to={`/offer-support-details/${props.data.Id}/landing`}>
              <ImageContainer data={props.data} title={props.Title}/>
            </Link>
          </CardMedia>
        <CardContent>
        </CardContent>
        <CardActions  className="MuiCardActions-root">

        </CardActions>

      </Card>
    </div>
  )

}
