import React from 'react';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import YoutubePlayer from './YoutubePlayer';
import MovieIcon from '@material-ui/icons/VideoCall';
import ReactBSAlert from "react-bootstrap-sweetalert";
import Button from '@material-ui/core/Button';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAltOutlined';
import FavoriteIcon from '@material-ui/icons/Favorite';
import IconButton from '@material-ui/core/IconButton';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import './VideoCard.css';

class VideoCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      defaultModal: false,
      notificationModal: false,
      formModal: false,
      buttonColor: 'info',
      voteChosen: false,
      alert: null,
      show: false
    };
    this.handler = this.handler.bind(this);
  }

  handler(e){
    this.props.onRef(e);
  }

  showAlert = () => {
    alert('Child Class Component');
  }
  
  toggleModal = state => {
    this.setState({
      [state]: !this.state[state]
    });
  }

  hideAlert = () => {
    this.setState({
      alert: null,
      show: false
    });
  }

  voteChangeEvent = (event) => {
    this.setState({ show: true, buttonColor: 'primary' });
    console.log('Child voteChangeEvent ', this.props);
    this.props.onChangeVote(this.props.data.Id);
    const userId = sessionStorage.getItem('userId');
    // Record Vote
    // Create or update a record
  }

  
  render() {
    return (
      <React.Fragment>
        <Card className='root'>
          <CardHeader 
            title={this.props.title}            
            titleTypographyProps={{variant:'h6' }}
          />
          <CardMedia>
            {/*  <Player url={videoURL} /> */}
            <YoutubePlayer url={this.props.url} />
          </CardMedia>
          <CardContent>
          </CardContent>
          <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
        
      </CardActions>   

        </Card>
        <ReactBSAlert
          show={this.state.show}
          success
          style={{ display: "block", marginTop: "100px" }}
          title="Success"
          onConfirm={() => this.hideAlert()}
          onCancel={() => this.hideAlert()}
          confirmBtnBsStyle="success"
          confirmBtnText="OK"
          btnSize=""
        >
        <div className="alert-messsage">
          <span>Your vote for <span style={{color:'darkred', fontWeight: 'bold'}}>{this.props.title}</span> has been recorded!</span>
          <span className="messsage-line">You can change your vote until Feb 9<sup>th</sup>, 2020</span>
        </div>
      </ReactBSAlert>        
    </React.Fragment>
    );
    
  }
}
export default VideoCard;