import React from 'react';
import { BrowserRouter as Router, Switch, Route, useLocation, Redirect  } from 'react-router-dom';
import { createBrowserHistory } from "history";
// import SearchParentLayout from '../Components/Search/SearchParentLayout';
// import { HashRouter } from 'react-router-dom';
import SupportParentLayout from  '../Components/Support/SupportParentLayout';
import OfferSupportLayout from  '../Components/Support/OfferForm/OfferSupportLayout';
import RequestSupportLayout from  '../Components/Support/RequestForm/RequestSupportLayout';
import AdminLayout from '../Components/Admin/AdminLayout';
import Landing from '../Components/Support/Landing/Landing';
import VideosLayout from '../Components/Videos/VideosLayout';
import MasonryLayout from '../Components/Landing/MasonryLayout';
import OfferSupportDetailsForm from '../Components/Support/OfferDetailsPage/OfferSupportDetailsForm';
import SubmissionFormLanding from '../Components/Support/SubmissionFormLanding';
import ShoppingCartParentLayout from '../Components/ShoppingCart/ShoppingCartParentLayout';
import ConnectionsTable from '../Components/Connections/ConnectionsTable';
import AdminOfferDetailsForm from '../Components/Admin/AdminOfferForm/AdminOfferDetailsForm';
import AdminRequestDetailsForm from '../Components/Admin/AdminRequestForm/AdminRequestDetailsForm';
import ConnectionsParent from '../Components/Connections/ConnectionsParent';
import RequestSupportDetailsForm from '../Components/Support/RequestDetailsPage/RequestSupportDetailsForm';
import AdminConnectionDetails from '../Components/Admin/AdminConnectionsForm/AdminConnectionDetails';

const myComponent = ({ history }) => {

  history.listen((location, action) => {
      // location is an object like window.location
      console.log(action, location.pathname, location.state)
  });

  return <div>...</div>;
};
// import Parent from './../Components/Test/Parent';
export default function MainRouter(props) {
  
  const history = createBrowserHistory();
  const rootURL = '/app';
  const rootURLOLD = '/';
  


  return (
    <div>                 
      <Switch>
        <Route exact path="/" component={MasonryLayout} />      
        <Route path="/home" component={Landing} />                                
        <Route path="/new-request" component={SupportParentLayout} />      
        <Route path="/request-support" component={RequestSupportLayout} />              
        <Route path="/offer-support" component={OfferSupportLayout} />              
        <Route path="/offer-support-details/:id/:rtnloc" component={OfferSupportDetailsForm} />                      
        <Route path="/request-support-details/:id/:rtnloc" component={RequestSupportDetailsForm} />         
        <Route path="/welcome" component={Landing} />            
        <Route path="/landing" component={MasonryLayout} />                
        <Route path="/cart" component={ShoppingCartParentLayout} />             
        <Route path="/admin/:tabid" component={AdminLayout} />                        
        <Route path="/successful-connections" component={ConnectionsTable} />            
        <Route path="/admin-offer-details-form/:id/:rtnloc/:tabid" component={AdminOfferDetailsForm} />              
        <Route path="/admin-request-details-form/:id/:rtnloc/:tabid" component={AdminRequestDetailsForm} />          

        <Route path="/admin-connections-details-form/:id/:rtnloc/:tabid" component={AdminConnectionDetails} />                                  
      </Switch>    
    </div>
  );
}
