import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import Input from '@material-ui/core/Input';
import TextField from '@material-ui/core/TextField';

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600],
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

export default function InputCheckBox(props) {
   const [showInput, setShowInput] = React.useState(false);

  // const handleChange = (event) => {
  //   console.log(event);
  //   setChecked(event.target.checked);
  // };
  const handleChange = (event) => {
    var show = (showInput) ? false : true;
    setShowInput(show);
  }

  return (
    <React.Fragment>
      <FormGroup row style={{ maxWidth: "100%", marginTop: "10px" }}>           
      <FormControlLabel control={
        <Checkbox          
          color="primary"
          name={props.fieldName}
          onChange={props.handleCheckChange} checked={props.checkBoxState} name={props.fieldName}           
          inputProps={{ 'aria-label': 'checkbox with default color' }}
          onChange={handleChange}
        />
      } label="On Behalf of" />        
      {(showInput === true) ?
        <TextField value={props.OnBehalfOf} 
                   onChange={props.handleOBHChange} 
                   id="OnBehalfOf"                    
                   name="OnBehalfOf" 
                   label="Name" 
                   variant="outlined" 
                   size="small" />
      :
      ''
      }
      
      </FormGroup>
      
    </React.Fragment>    
  );
}
